import { Component, OnInit } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from './../../service/login.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {
  public message: any;
  public selectedUser: any;
  public alertMessage: any = [];
  public alertdata = false;
  resetPassword: FormGroup;
  submitted = false;
  public invalidPassword: boolean;

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private formBuilder: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params: Params) => {
      // console.log('params', params.username);
      this.selectedUser = params.username;
    });
    this.resetPassword = this.formBuilder.group({
      password: ['', [Validators.required, Validators.minLength(8)]],
      confirmPassword: ['', Validators.required]
    });
    // this.selectedUser = localStorage.getItem('selectedUsername');
    // localStorage.removeItem('selectedUsername');
  }

  get f() { return this.resetPassword.controls; }

  passwordFails(checks: boolean[]): boolean {
    let counter = 0;
    for (let i = 0; i < checks.length; i++) {
      if (checks[i]) {
        counter += 1;
      }
    }
    return counter < 4;
  }


  validatePassword(password: string) {
    let hasLower = false;
    let hasUpper = false;
    let hasNum = false;
    let hasSpecial = false;

    const lowercaseRegex = new RegExp("(?=.*[a-z])");// has at least one lower case letter
    if (lowercaseRegex.test(password)) {
      hasLower = true;
    }

    const uppercaseRegex = new RegExp("(?=.*[A-Z])"); //has at least one upper case letter
    if (uppercaseRegex.test(password)) {
      hasUpper = true;
    }

    const numRegex = new RegExp("(?=.*\\d)"); // has at least one number
    if (numRegex.test(password)) {
      hasNum = true;
    }

    const specialcharRegex = new RegExp("[!@#$%^&*(),.?\":{}|<>]");
    if (specialcharRegex.test(password)) {
      hasSpecial = true;
    }
    this.invalidPassword = this.passwordFails([hasLower, hasUpper, hasNum, hasSpecial]);
  }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.resetPassword.invalid) {
      return;
    }

    if (this.resetPassword.value.password == this.resetPassword.value.confirmPassword) {
      let newData = {
        password: this.resetPassword.value.password
      };
      this.loginService.changePassword(JSON.stringify(newData), this.selectedUser).subscribe(
        val => {
          // this.message = val;
          // this.message = this.message.message;
          this.alertdata = true;
          this.alertMessage = [{
            "type": "success", "close": true,
            "content": "<strong>Password changed successfully</strong>",
          }];
          // setTimeout(function () {
          //   this.alertdata = false;
          //   this.router.navigateByUrl('loginmain');
          // }.bind(this), 5000);
        },
        error => {
          this.message = error;
          this.message = this.message.error.message;
          // console.log('login', this.message);
          this.alertdata = true;
          this.alertMessage = [{
            "type": "danger", "close": true,
            "content": "<strong>" + this.message + "</strong>",
          }
          ];
          // setTimeout(function () { this.alertdata = false; }.bind(this), 5000);
        }
      );
    }

  }
}
