import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from '../../service/login.service';
import { AutomationFactoryService } from '../../service/automation-factory.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription, timer } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { environment } from './../../../environments/environment';
import { JsonPipe } from '@angular/common';

@Component({
  selector: 'app-automation-factory',
  templateUrl: './automation-factory.component.html',
  styleUrls: ['./automation-factory.component.scss']
})
export class AutomationFactoryComponent implements OnInit {
  user: any;
  roles: any;
  orders: any;
  ordersSheduler: any;
  totalOrders: any;
  totalOrdersScheduler: any;
  totalCount: any;
  completedCount: any;
  acceptedCount: any;
  failedCount: any;
  scheduledCount: any;
  subscription: Subscription;
  orderListActive = true;
  scheduleListActive = false;
  refresh = true;
  schedulerRefresh = true;
  total = true;
  accepted = false;
  completed = false;
  failed = false;
  scheduled = false;
  scheduleTotal = true;
  inactive = false;
  active = false;
  deleted = false;
  orderCount: any;
  refreshList = true;
  display = 'none';
  public adminReportStart = environment.adminReportStart;
  public adminReportEnd = environment.adminReportEnd;
  public adminSchedulerReportStart = environment.adminSchedulerReportStart;
  public adminSchedulerReportEnd = environment.adminSchedulerReportEnd;
  public reportStart = environment.reportStart;
  public reportMid = environment.reportMid;
  public reportEnd = environment.reportEnd;
  public schedulerReportStart = environment.schedulerReportStart;
  public schedulerReportMid = environment.schedulerReportMid;
  public schedulerReportEnd = environment.schedulerReportEnd;
  scheduledOrders: any;
  menuList: any;
  orderList = false;
  scheduledJobs = false;
  orderReport = false;
  schedulerReport = false;
  createOrder = false;
  adminFlag = false;
  // reportClicked = false;
  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private automationFactoryService: AutomationFactoryService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('USER'));
    this.roles = JSON.parse(localStorage.getItem('ROLES'));
    let r = this.roles.filter(item => item.rolePermission.permission === 'Admin');
    if (r.length > 0) {
      this.adminFlag = true;
    }
    // console.log(this.user);
    if (!this.user) {
      this.router.navigateByUrl('loginmain');
    }
    this.menuList = JSON.parse(localStorage.getItem('MENU'));
    this.menuList = this.menuList.filter(item => item.mainMenu.menuName === 'Automation factory');
    this.menuList = this.menuList[0].subMenu;
    let j = this.menuList.filter(item => item.menuName === 'Orders');
    if (j.length > 0) {
      this.orderList = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Scheduled Jobs');
    if (j.length > 0) {
      this.scheduledJobs = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Create Order');
    if (j.length > 0) {
      this.createOrder = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Order Report');
    if (j.length > 0) {
      this.orderReport = true;
      setTimeout(function () {
        if (this.adminFlag) {
          let link = this.adminReportStart + 'refreshInterval:(pause:!t,value:0),' + this.adminReportEnd;
          (<HTMLIFrameElement>document.getElementById('iframe')).src = link;
        } else {
          let link = this.reportStart + 'refreshInterval:(pause:!t,value:0),' + this.reportMid + "query:(language:kuery,query:'"
          this.roles.forEach((element, index) => {
            if (index == 0) {
              link = link + "role.keyword : " + element.roleCode;
            } else {
              link = link + " or role.keyword : " + element.roleCode;
            }
          });
          link = link + "')," + this.reportEnd;
          (<HTMLIFrameElement>document.getElementById('iframe')).src = link;
        }
      }.bind(this), 1000);
    }
    j = this.menuList.filter(item => item.menuName === 'Scheduled Jobs Report');
    if (j.length > 0) {
      this.schedulerReport = true;
      setTimeout(function () {
        if (this.adminFlag) {
          let schedulerLink = this.adminSchedulerReportStart + 'refreshInterval:(pause:!t,value:0),' + this.adminSchedulerReportEnd;
          (<HTMLIFrameElement>document.getElementById('scheduleriframe')).src = schedulerLink;
        } else {
          let schedulerLink = this.schedulerReportStart + 'refreshInterval:(pause:!t,value:0),' + this.schedulerReportMid + "query:(language:kuery,query:'"
          this.roles.forEach((element, index) => {
            if (index == 0) {
              schedulerLink = schedulerLink + "Role.keyword : " + element.roleCode;
            } else {
              schedulerLink = schedulerLink + " or Role.keyword : " + element.roleCode;
            }
          });
          schedulerLink = schedulerLink + "')," + this.schedulerReportEnd;
          (<HTMLIFrameElement>document.getElementById('scheduleriframe')).src = schedulerLink;
        }

      }.bind(this), 1000);
    }
    if (this.orderList) {
      this.subscription = timer(0, 600000).pipe(
        switchMap(() => this.automationFactoryService.getAllOrders(this.roles))
      ).subscribe(val => {
        this.orders = val;
        this.total = true;
        this.accepted = false;
        this.completed = false;
        this.failed = false;
        this.scheduled = false;
        this.inactive = false;
        this.active = false;
        this.deleted = false;
        this.totalOrders = JSON.parse(JSON.stringify(this.orders));
        this.totalCount = this.orders.length;
        let j = this.orders.filter(item => item.orderStatus === 'Completed' || item.orderStatus === 'completed');
        this.completedCount = j.length;
        j = this.orders.filter(item => item.orderStatus === 'Accepted');
        this.acceptedCount = j.length;
        j = this.orders.filter(item => item.orderStatus === 'Failed');
        this.failedCount = j.length;
        j = this.orders.filter(item => item.schedulerId !== 0);
        this.scheduledCount = j.length;
        // console.log('orders', val);
      });
    }

    if (this.scheduledJobs) {
      this.subscription = timer(0, 600000).pipe(
        switchMap(() => this.automationFactoryService.getScheduleList(this.roles))
      ).subscribe(val1 => {
        this.scheduledOrders = val1;
        this.totalOrdersScheduler = JSON.parse(JSON.stringify(this.scheduledOrders));
      });
    }
  }
  @Output() actionEvent = new EventEmitter();
  updateorderList() {
    AutomationFactoryService.refreshlist.emit();
    this.actionEvent.emit();
    this.ngOnInit();
  }

  filterBasedonStatus(status) {
    if (status === 'total') {
      this.total = true;
      this.accepted = false;
      this.completed = false;
      this.failed = false;
      this.scheduled = false;
      this.orders = JSON.parse(JSON.stringify(this.totalOrders));
    } else if (status === 'accepted') {
      this.total = false;
      this.accepted = true;
      this.completed = false;
      this.failed = false;
      this.scheduled = false;
      this.orders = JSON.parse(JSON.stringify(this.totalOrders.filter(item => item.orderStatus === 'Accepted')));
    } else if (status === 'completed') {
      this.total = false;
      this.accepted = false;
      this.completed = true;
      this.failed = false;
      this.scheduled = false;
      this.orders = JSON.parse(JSON.stringify(this.totalOrders.filter(item => item.orderStatus === 'Completed' || item.orderStatus === 'completed')));
    } else if (status === 'scheduled') {
      this.total = false;
      this.accepted = false;
      this.completed = false;
      this.failed = false;
      this.scheduled = true;
      this.orders = JSON.parse(JSON.stringify(this.totalOrders.filter(item => item.schedulerId !== 0)));
    } else if (status === 'failed') {
      this.total = false;
      this.accepted = false;
      this.completed = false;
      this.failed = true;
      this.scheduled = false;
      this.orders = JSON.parse(JSON.stringify(this.totalOrders.filter(item => item.orderStatus === 'Failed')));
    }
    this.orderCount = this.orders.length;
  }

  filterBasedonSheduleStatus(status) {
    if (status === 'total') {
      this.scheduleTotal = true;
      this.inactive = false;
      this.active = false;
      this.deleted = false;
      this.scheduledOrders = JSON.parse(JSON.stringify(this.totalOrdersScheduler));
    } else if (status === 'inactive') {
      this.scheduleTotal = false;
      this.inactive = true;
      this.active = false;
      this.deleted = false;
      this.scheduledOrders = JSON.parse(JSON.stringify(this.totalOrdersScheduler.filter(item => item.jobStatus === 'Inactive')));
    } else if (status === 'active') {
      this.scheduleTotal = false;
      this.inactive = false;
      this.active = true;
      this.deleted = false;
      this.scheduledOrders = JSON.parse(JSON.stringify(this.totalOrdersScheduler.filter(item => item.jobStatus === 'Active')));
    }  else if (status === 'deleted') {
      this.scheduleTotal = false;
      this.inactive = false;
      this.active = false;
      this.deleted = true;
      this.scheduledOrders = JSON.parse(JSON.stringify(this.totalOrdersScheduler.filter(item => item.jobStatus === 'Deleted')));
    }

  }

  refreshOrderList() {
    // this.display = 'block';
    this.refreshList = false;
    this.automationFactoryService.getAllOrders(this.roles).subscribe(
      val => {
        setTimeout(function () {
          this.refreshList = true;
        }.bind(this), 2000);
        this.orders = val;
        this.total = true;
        this.accepted = false;
        this.completed = false;
        this.failed = false;
        this.scheduled = false;
        this.totalOrders = JSON.parse(JSON.stringify(this.orders));
        this.totalCount = this.orders.length;
        let j = this.orders.filter(item => (item.orderStatus === 'Completed' || item.orderStatus === 'completed'));
        this.completedCount = j.length;
        j = this.orders.filter(item => item.orderStatus === 'Accepted');
        this.acceptedCount = j.length;
        j = this.orders.filter(item => item.orderStatus === 'Failed');
        this.failedCount = j.length;
        j = this.orders.filter(item => item.schedulerId !== 0);
        this.scheduledCount = j.length;
      });
  }

  reportRefresh() {
    this.refresh = false;
    // console.log('refresh');
    if (this.adminFlag) {
      let link = this.adminReportStart + 'refreshInterval:(pause:!f,value:1000),' + this.adminReportEnd;
      (<HTMLIFrameElement>document.getElementById('iframe')).src = link;
      setTimeout(function () {
        this.refresh = true;
        link = this.adminReportStart + 'refreshInterval:(pause:!t,value:0),' + this.adminReportEnd;
        (<HTMLIFrameElement>document.getElementById('iframe')).src = link;
      }.bind(this), 5000);
    } else {
      let link = this.reportStart + 'refreshInterval:(pause:!f,value:1000),' + this.reportMid + "query:(language:kuery,query:'"
      this.roles.forEach((element, index) => {
        if (index == 0) {
          link = link + "role.keyword : " + element.roleCode;
        } else {
          link = link + " or role.keyword : " + element.roleCode;
        }
      });
      link = link + "')," + this.reportEnd;
      (<HTMLIFrameElement>document.getElementById('iframe')).src = link;
      setTimeout(function () {
        this.refresh = true;
        link = this.reportStart + 'refreshInterval:(pause:!t,value:0),' + this.reportMid + "query:(language:kuery,query:'"
        this.roles.forEach((element, index) => {
          if (index == 0) {
            link = link + "role.keyword : " + element.roleCode;
          } else {
            link = link + " or role.keyword : " + element.roleCode;
          }
        });
        link = link + "')," + this.reportEnd;
        (<HTMLIFrameElement>document.getElementById('iframe')).src = link;
      }.bind(this), 5000);
    }
  }

  schedulerReportRefresh() {
    this.schedulerRefresh = false;
    // console.log('refresh');
    if (this.adminFlag) {
      let link = this.adminSchedulerReportStart + 'refreshInterval:(pause:!f,value:1000),' + this.adminSchedulerReportEnd;
      (<HTMLIFrameElement>document.getElementById('scheduleriframe')).src = link;
      setTimeout(function () {
        this.schedulerRefresh = true;
        link = this.adminSchedulerReportStart + 'refreshInterval:(pause:!t,value:0),' + this.adminSchedulerReportEnd;
        (<HTMLIFrameElement>document.getElementById('scheduleriframe')).src = link;
      }.bind(this), 5000);
    } else {
      let link = this.schedulerReportStart + 'refreshInterval:(pause:!f,value:1000),' + this.schedulerReportMid + "query:(language:kuery,query:'"
      this.roles.forEach((element, index) => {
        if (index == 0) {
          link = link + "role.keyword : " + element.roleCode;
        } else {
          link = link + " or role.keyword : " + element.roleCode;
        }
      });
      link = link + "')," + this.schedulerReportEnd;
      (<HTMLIFrameElement>document.getElementById('scheduleriframe')).src = link;
      setTimeout(function () {
        this.schedulerRefresh = true;
        link = this.schedulerReportStart + 'refreshInterval:(pause:!t,value:0),' + this.schedulerReportMid + "query:(language:kuery,query:'"
        this.roles.forEach((element, index) => {
          if (index == 0) {
            link = link + "role.keyword : " + element.roleCode;
          } else {
            link = link + " or role.keyword : " + element.roleCode;
          }
        });
        link = link + "')," + this.schedulerReportEnd;
        (<HTMLIFrameElement>document.getElementById('scheduleriframe')).src = link;
      }.bind(this), 5000);
    }
  }
}
