  calculateRatio() {
    this.ratio = (this.intakeCreate.value.dataInterpretation * 1) + (this.intakeCreate.value.manualActivity * 1) + (this.intakeCreate.value.decisionMaking * 1);
    this.ratio = this.intakeCreate.value.aet - this.ratio;
    this.ratio = (this.ratio * 100) / this.intakeCreate.value.aet;
    this.ratio = this.ratio.toFixed();
    this.intakeCreate.patchValue({
      automationRatio: this.ratio,
      dataInterpretation: this.intakeCreate.value.dataInterpretation * 1,
      manualActivity: this.intakeCreate.value.manualActivity * 1,
      decisionMaking: this.intakeCreate.value.decisionMaking * 1,
    });
  }
  limitNBI(data) {
    if(data == 0){
      this.nbiRoleInvalid = false;
      this.otherApplicationName = false;
    }
    else{
      this.nbiRoleInvalid = true;
    }
    this.nbitypeRole=[];
    this.othernbiValue = "";
    this.nbiTypeArray =[];
    this.NBItableLength = [];
    this.NBItableLength.length = data;
    this.intakeCreate.patchValue({
      typeOfNBI: '',
    });
    this.nbiTypeDropdownSettings = Object.assign({}, this.nbiTypeDropdownSettings, { limitSelection: this.intakeCreate.value.noOfNBI * 1 });
  }
  limitSBI(data) {
    if(data == 0){
      this.sbiRoleInvalid = false;
      this.otherDeviceName = false;
    }
    else{
      this.sbiRoleInvalid = true;
    }
    this.sbitypeRole=[];
    this.othersbiValue
    this.othersbiValue = "";
    this.sbitype = [];
    this.SBItableLength = [];
    this.SBItableLength.length = data;
    this.intakeCreate.patchValue({
      typeOfSBI: '',
    });
    this.sbiTypeDropdownSettings = Object.assign({}, this.sbiTypeDropdownSettings, { limitSelection: this.intakeCreate.value.noOfSBI * 1 });
  }
