  viewSummary() {
    this.savingsReturned = false;
    if (this.summaryOpened === true) {
      this.summaryOpened = false;
    } else {
      this.selectedComplexity = this.complexityList.filter(item => item.complexityCode === (this.intakeCreate.value.requestedComplexity * 1));
      this.selectedComplexity = this.selectedComplexity[0];
      this.summaryOpened = true;
      // if (this.intakeCreate.value.noOfDevices === "" || !this.intakeCreate.value.noOfDevices) {
      //   this.intakeCreate.value.noOfDevices = 0;
      // }
      if (this.intakeCreate.value.noOfSBI === "" || !this.intakeCreate.value.noOfSBI) {
        this.intakeCreate.value.noOfSBI = 0;
      }
      // if (this.intakeCreate.value.noOfSystems === "" || !this.intakeCreate.value.noOfSystems) {
      //   this.intakeCreate.value.noOfSystems = 0;
      // }
      if (this.intakeCreate.value.noOfNBI === "" || !this.intakeCreate.value.noOfNBI) {
        this.intakeCreate.value.noOfNBI = 0;
      }
      let getComplexityValues = {
        // noOfDevices: (this.intakeCreate.value.noOfDevices * 1),
        noOfNorthBoundInterface: (this.intakeCreate.value.noOfNBI * 1),
        noOfSouthBoundInterface: (this.intakeCreate.value.noOfSBI * 1),
        // noOfSystems: (this.intakeCreate.value.noOfSystems * 1)
      };
      this.intakeService.getComplexity(getComplexityValues).subscribe(
        val => {
          this.complexity = val;
          let roiCalcValues = {
            actualFTE: this.intakeCreate.value.actualFTE,
            averageDailyVolume: this.intakeCreate.value.averageDailyVolume,
            aet: this.intakeCreate.value.aet,
            noOfWorkingDays: 22,
            noOfMonths: this.intakeCreate.value.noOfMonths,
            timeDuration: this.complexity.timeDuration,
            costPerFTE: this.intakeCreate.value.fteRate,
            ftecostPerAutomation: 50,
            costPerAutomation: this.complexity.costPerAutomation
          };
          this.intakeService.getROICalculation(roiCalcValues).subscribe(
            val1 => {
              this.savingsReturned = true;
              this.calcValues = val1;
              this.monthlyRevenue = this.calcValues.monthlyRevenue;
              this.calcValues = this.calcValues.calculation;
              this.manualHours = this.calcValues.manualHours.toFixed();
              this.manualCost = this.calcValues.manualCost.toFixed();
              // this.automationHours = this.calcValues.automationHours.toFixed();
              this.automationCost = this.calcValues.automationCost.toFixed();
              this.hoursSaving = this.calcValues.hoursSaving.toFixed();
              this.costSaving = this.calcValues.costSaving.toFixed();
              this.roiPerc = this.calcValues.roiPerc.toFixed();
              this.revenue = this.calcValues.revenue.toFixed();
              console.log('complexity', val1);
            });
          console.log('complexity', val);

        });
    }
  }

