  public downloadFile(file, intakeid) {
    return this.http.get(this.url8080 + '/file/download/' + file.fileName,{headers: this.headers});
  }

  public deleteFile(file, intakeid) {
    return this.http.delete(this.url8080 + '/file/remove/' + intakeid + '/' + file.fileId,{headers: this.headers});
  }

  public fileUpload(files, intakeid) {
    const HttpUploadOptions = {
      headers: new HttpHeaders({ "Accept": "application/json" })
    }
    let head= this.headers;
    // let head= new HttpHeaders();
    head = head.append('Accept', 'application/json');
    var formData = new FormData();
    for(var index = 0; index < files.length; index++) {
        formData.append("files", files[index]);
    }
    let params = formData;
    return this.http.post(this.url8080 + '/file/upload/' + intakeid, params, {headers: head}).pipe(map(val => {
      return val;
    })
    );
  }
