import { Component, OnInit } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from './../../service/login.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { environment } from './../../../environments/environment';

@Component({
  selector: 'app-forgot-password-username',
  templateUrl: './forgot-password-username.component.html',
  styleUrls: ['./forgot-password-username.component.scss']
})
export class ForgotPasswordUsernameComponent implements OnInit {

  public message: any;
  public username: any = '';
  public selectedUser: any;
  public usernameCheck: any = false;
  public alertMessage: any = [];
  public userList: any;
  public alertdata = false;
  public baseUrl = environment.baseUrl;

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    // this.loginService.getAllUsers().subscribe(
    //   val => {
    //     this.userList = val;
    //     // console.log('users', this.userList);
    //   });
  }

  onFPSubmit() {
    this.username = (<HTMLSelectElement>document.getElementById("username_FP")).value;
    this.username = this.username.trim();
    if (this.username === '') {
      this.usernameCheck = true;
      return;
    } else {
      this.usernameCheck = false;
      if (this.username.includes('@')) {
        this.selectedUser = this.userList.filter(item => item.email === this.username);
      } else {
        this.selectedUser = this.userList.filter(item => item.username === this.username);
      }
      if (this.selectedUser.length > 0) {
        let maildata = {
          mailContent: 'Click on the <a href=`' + this.baseUrl + '#/forgotPassword?username=' + this.selectedUser[0].username + '`>link</a> to reset password.',
          // mailContent: 'Click on the <a href=`http://10.168.160.65:8080/rogers_prototype/#/forgotPassword?username=' + this.selectedUser[0].username + '`>link</a> to reset password.',
          to: '',
          cc: '',
          Bcc: '',
          testingRecipients: {
            to: this.selectedUser[0].email,
            cc: '',
            Bcc: ''
          },
          subject: 'NETBOTS.AI-Password Reset Link'
        };
        this.loginService.sendMail(JSON.stringify(maildata)).subscribe(
          val => {
            this.message = val;
            if (this.message.response == 'Mail Sent Successfully') {
              this.alertdata = true;
              this.alertMessage = [{
                "type": "success", "close": true,
                "content": "<strong>Mail </strong>sent successfully!",
              }];
              setTimeout(function () {
                this.alertdata = false;
                // this.router.navigateByUrl('');
                //window.location.reload();
                 
              }.bind(this), 1000);
            } else {
              this.alertdata = true;
              this.alertMessage = [{
                "type": "danger", "close": true,
                "content": "<strong>Failed</strong> to send mail",
              }
              ];
              // setTimeout(function () { this.alertdata = false; }.bind(this), 5000);
            }
          });
      } else {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong>Username/Email doesn't exist</strong>",
        }
        ];
        // setTimeout(function () { this.alertdata = false; }.bind(this), 5000);
      }
    }
  }

}
