import { Component, OnInit, Input, Output, ElementRef, ViewChild, OnChanges, EventEmitter, SimpleChanges, SimpleChange } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { IntakeService } from './../../../service/intake.service';
import { Router } from '@angular/router';
import { environment } from './../../../../environments/environment';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-intake-view',
  templateUrl: './intake-view.component.html',
  styleUrls: ['./intake-view.component.scss']
})
export class IntakeViewComponent implements OnInit {
  @Input() intakeData: any;
  public serviceUrl = environment.serviceUrl;
  summaryOpened = false;
  complexity: any;
  manualCost: any;
  jiraurl:any=environment.jiraurl
  deviceapplicationNames:any='';
  applicationNames:any='';
  automationCost: any;
  businessBenfitslist:any;
  automationtable=false
  revenue: any;
  costDifference: any;
  roiPerc: any;
  manualHours: any;
  automationHours: any;
  costSaving: any;
  hoursSaving: any;
  sbiList: any;
  nbiList: any;
  complexityList: any;
  businessUnitList: any;
  selectedComplexity: any;
  calcValues: any;
  nbiTypeSelected: any = [];
  sbiTypeSelected: any = [];
  OtherIndexSbi:any;
  OtherIndexNbi:any;
  jiraId:any;

  constructor(
    public http: HttpClient,
    private intakeService: IntakeService,
    private router: Router,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
    // console.log('view', this.intakeData);
    this.generateSafeUrl();
    this.intakeService.getComplexityList().subscribe(
      val => {
        this.complexityList = val;
        this.selectedComplexity = this.complexityList.filter(item => item.complexityCode === (this.intakeData.rfEffort.requestedComplexity * 1));
        this.selectedComplexity = this.selectedComplexity[0];
      });
    this.nbiTypeSelected = this.intakeData.rfEffort.typeOfNBI;
    this.sbiTypeSelected = this.intakeData.rfEffort.typeOfSBI;
    console.log("viewintake",this.intakeData)
   
    // this.intakeService.businessBenfitslist().subscribe(data=>
    //   {
    //     alert('this is apo')
    //     this.automationtable=true
    //    this.businessBenfitslist=data
    //    console.log("businedss",this.businessBenfitslist)
    //   }
    // )
    this.intakeData.rfEffort.device.filter(item=>
      {
        if(item.hasOwnProperty("otherFlag")==true)
        {
          if(item.otherFlag!='')
          {
          this.deviceapplicationNames=item.devicename
          }
        }
      })

      this.intakeData.rfEffort.application.filter(item=>
        {
          if(item.hasOwnProperty("otherFlag")==true)
          {
            if(item.otherFlag!='')
            {
              this.applicationNames=item.applicationName
            }
           
          }
        })
    // for(var i=0;i<this.sbiTypeSelected.length;i++){
    //   if(this.sbiTypeSelected[i].sbitype == 'Other (Specify)'){
    //     this.OtherIndexSbi = i;
    //     }
    // }
    // for(var i=0;i<this.nbiTypeSelected.length;i++){
    //   if(this.nbiTypeSelected[i].nbitype == 'Other (Specify)'){
    //     this.OtherIndexNbi = i;
    //     }
    // }
    if(this.intakeData.jirakey == null){
      this.jiraId = "NA";
    }
    else{
      this.jiraId = this.intakeData.jirakey;
    }
  }

  generateSafeUrl(): SafeUrl {       
    let safeurl=this.jiraurl+this.jiraId
    return this.sanitizer.bypassSecurityTrustUrl(safeurl);
  }    
    
  public greaterThan(subj, num) {
    return subj > num;
  }

  public lesserThan(subj, num) {
    return subj < num;
  }
  public setStyle(subj, num) {
    var styleObj={'background-color':''}
   
        if(subj==num)
        {
         styleObj['background-color']="#d0d0c5"
        }
        if(subj>num)
        {
         styleObj['background-color']="#03bd5a"
        }
        if(subj<num)
        {
         styleObj['background-color']="#ff9947"
        }
        return styleObj
        
     }

  viewSummary() {
    if (this.summaryOpened === true) {
      this.summaryOpened = false;
    } else {
      this.selectedComplexity = this.complexityList.filter(item => item.complexityCode === (this.intakeData.rfEffort.requestedComplexity * 1));
      this.selectedComplexity = this.selectedComplexity[0];
      this.summaryOpened = true;
      if (this.intakeData.rfEffort.noOfDevices === "" || !this.intakeData.rfEffort.noOfDevices) {
        this.intakeData.rfEffort.noOfDevices = 0;
      }
      if (this.intakeData.rfEffort.noOfSBI === "" || !this.intakeData.rfEffort.noOfSBI) {
        this.intakeData.rfEffort.noOfSBI = 0;
      }
      if (this.intakeData.rfEffort.noOfSystems === "" || !this.intakeData.rfEffort.noOfSystems) {
        this.intakeData.rfEffort.noOfSystems = 0;
      }
      if (this.intakeData.rfEffort.noOfNBI === "" || !this.intakeData.rfEffort.noOfNBI) {
        this.intakeData.rfEffort.noOfNBI = 0;
      }
      let getComplexityValues = {
        noOfDevices: (this.intakeData.rfEffort.noOfDevices * 1),
        noOfNorthBoundInterface: (this.intakeData.rfEffort.noOfNBI * 1),
        noOfSouthBoundInterface: (this.intakeData.rfEffort.noOfSBI * 1),
        noOfSystems: (this.intakeData.rfEffort.noOfSystems * 1)
      };
      this.intakeService.getComplexity(getComplexityValues).subscribe(
        val => {
          this.complexity = val;
          let roiCalcValues = {
            actualFTE: this.intakeData.rfEffort.actualFTE,
            averageDailyVolume: this.intakeData.rfEffort.avgDailyVolume,
            aet: this.intakeData.rfEffort.aet,
            noOfWorkingDays: this.intakeData.rfEffort.noOfWorkingDays,
            noOfMonths: this.intakeData.rfEffort.noOfMonths,
            timeDuration: this.complexity.timeDuration,
            costPerFTE: this.intakeData.rfEffort.fteRate.fteRateId,
            ftecostPerAutomation: 50,
            costPerAutomation: this.complexity.costPerAutomation
          };
          this.intakeService.getROICalculation(roiCalcValues).subscribe(
            val1 => {
              this.calcValues = val1;
              this.calcValues = this.calcValues.calculation;
              this.manualHours = this.calcValues.manualHours.toFixed();
              this.manualCost = this.calcValues.manualCost.toFixed();
              // this.automationHours = this.calcValues.automationHours.toFixed();
              this.automationCost = this.calcValues.automationCost.toFixed();
              this.hoursSaving = this.calcValues.hoursSaving.toFixed();
              this.costSaving = this.calcValues.costSaving.toFixed();
              this.roiPerc = this.calcValues.roiPerc.toFixed();
              this.revenue = this.calcValues.revenue.toFixed();
            });
          // console.log('complexity', val);
        });
    }
  }

}
