import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login-main/login/login.component';
import { LoginMainComponent } from './login-main/login-main.component';
import { MainComponent } from './main/main.component';
import { SignupComponent } from './login-main/signup/signup.component';
import { ForgotPasswordUsernameComponent } from './login-main/forgot-password-username/forgot-password-username.component';
import { ForgotPasswordComponent } from './login-main/forgot-password/forgot-password.component';


const routes: Routes = [
  { path: '', redirectTo: 'loginmain', pathMatch: 'full'},
  // { path: 'login', component: LoginComponent, data: { animation: 'isRight' } },
  // { path: 'signup', component: SignupComponent, data: { animation: 'isLeft' } },
  // { path: 'forgotPasswordUsername', component: ForgotPasswordUsernameComponent },
  { path: 'forgotPassword', component: ForgotPasswordComponent },
  { path: 'main', component: MainComponent },
  { path: 'loginmain', component: LoginMainComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
