import { Component, OnInit } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from '../service/login.service';
import { IntakeService } from '../service/intake.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
  providers: [DatePipe]
})
export class MainComponent implements OnInit {
  user: any;
  roles: any;
  menuList: any;
  public tabHeading = "Intake";
  adminConsole = false;
  dashboard = false;
  intakePortal = false;
  automationFactory = false;

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private intakeService: IntakeService,
    private formBuilder: FormBuilder,
    private datePipe: DatePipe,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('USER'));
    this.roles = JSON.parse(localStorage.getItem('ROLES'));
    
    // console.log(this.user);
    if (!this.user) {
      this.router.navigateByUrl('loginmain');
    }
    this.menuList = JSON.parse(localStorage.getItem('MENU'));
    let j = this.menuList.filter(item => item.mainMenu.menuName === 'Admin Portal');
    if (j.length > 0) {
      this.adminConsole = true;
    }
    j = this.menuList.filter(item => item.mainMenu.menuName === 'Automation factory');
    if (j.length > 0) {
      this.automationFactory = true;
    }
    j = this.menuList.filter(item => item.mainMenu.menuName === 'Intake Portal');
    if (j.length > 0) {
      this.intakePortal = true;
    }
    j = this.menuList.filter(item => item.mainMenu.menuName === 'Dashboard');
    if (j.length > 0) {
      this.dashboard = true;
    }
  }

  setHeading(heading) {
    this.tabHeading = heading;
  }

  logout() {
    localStorage.removeItem('USER');
    localStorage.removeItem('MENU');
    localStorage.removeItem('ROLES');
    localStorage.removeItem('basicAuth');
    this.router.navigateByUrl('loginmain');
  }
}
