  /*****Cost Weitage calculation*******/
  costWeitageCal(event:number,botIndex:number,suitIndex:number){
    if(botIndex == -1){
      for(var k=0; k<this.costWeitage[suitIndex].length; k++){
        this.costWeitage[suitIndex][k] = event;
      }
    }
    for(var i=0; i<=suitIndex; i++){
      if(!this.costWeitage[suitIndex]){
        this.costWeitage[suitIndex]=[];
      }
      for(var j=0; j<=botIndex; j++){
        this.costWeitage[suitIndex][botIndex] = event;
      }
      var sum = 0;
      for (var i = 0; i < this.costWeitage.length; i++) {
         for (var j = 0; j < this.costWeitage[i].length; j++) {
          if(this.costWeitage[i][j]){
            sum += Number(this.costWeitage[i][j]);
          }
        }
      }
    }
    this.enterdCostSum = sum;
    if(sum == 100){
      this.hideCostWeitage = true;
      this.errorValue = 0 ;
      this.errorAlert = false;
    }
    else if(sum > 100){
      this.hideCostWeitage = true;
      this.errorAlert = true;
      let diff = sum - 100;
      this.errorValue = event - diff;
    }
    else{
      this.errorValue = 0 ;
      this.hideCostWeitage = false;
      this.errorAlert = false;
    }
  }

