import { Component, OnInit, Input, OnChanges,ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LoginService } from '../../../service/login.service';
import { AutomationFactoryService } from '../../../service/automation-factory.service';
import { Router } from '@angular/router';
import { FormBuilder } from '@angular/forms';
import { Subscription, timer } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { environment } from './../../../../environments/environment';
@Component({
  selector: 'app-scheduled-job-list',
  templateUrl: './scheduled-job-list.component.html',
  styleUrls: ['./scheduled-job-list.component.scss'],
  providers: [AutomationFactoryService]
})
export class ScheduledJobListComponent implements OnInit {
  
  @Input() scheduledOrderList: any;
  pageOfItems: Array<any>;
  pageSize: any;
  roles: any;
  suitDetails:any;
  schedularDetails:any;
  toggle=true;
  activeId:any;

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private automationFactoryService: AutomationFactoryService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.roles = JSON.parse(localStorage.getItem('ROLES'));
    // console.log('scheduledOrderListon', this.scheduledOrderList)
  }

  ngOnChanges(): void {
    this.pageSize = 10;
    // console.log('scheduledOrderList', this.scheduledOrderList);
  }

  onChangePage(pageOfItems: Array<any>) {
    this.pageOfItems = pageOfItems;
  }
 
 deleteScheduler(id){
    this.automationFactoryService.deleteSchedule(id).subscribe(val => {

      // console.log("check_log",this.scheduledOrderList[0].jobStatus);
      for(var i in this.scheduledOrderList)
      {
        if(this.scheduledOrderList[i].schedulerId==id)
        {
          this.scheduledOrderList[i].jobStatus='Deleted';
        }


      }
      
    });
  }

  changeStatus(event, id, index){
    this.toggle = false;
    if (event.target.checked) {
      this.automationFactoryService.resumeSchedule(id).subscribe(val => {
        for(var i in this.scheduledOrderList)
        {
          if(this.scheduledOrderList[i].schedulerId==id)
          {
            this.scheduledOrderList[i].jobStatus='Active';
          }


        }
        // this.automationFactoryService.getScheduleList(this.roles).subscribe(val1 => {
        //   this.scheduledOrderList = val1;
        // });
      });
    } else {
      this.automationFactoryService.pauseSchedule(id).subscribe(val => {
        for(var i in this.scheduledOrderList)
        {
          if(this.scheduledOrderList[i].schedulerId==id)
          {
            this.scheduledOrderList[i].jobStatus='Inactive';
          }


        }
        
      });
    }
  }

  viewScheduledJob(event,index){
    this.automationFactoryService.getSchedularDetails(event).subscribe(val=>{
      this.suitDetails = val;
      this.suitDetails = this.suitDetails[0];
      this.toggle = true;
 })
}

  scheduleEdit(event,index){
    this.automationFactoryService.getSchedularDetails(event).subscribe(val=>{
      this.schedularDetails = val;
      for(var i=0; i<this.schedularDetails.length; i++){
        this.schedularDetails = this.schedularDetails[i];
      }
      this.activeId = index;
      this.toggle = true;
    })
  }
  stopCollapse(){
    console.log('collapse');
  }

  public ascScheduleIdSort = true;
  public ascOrderTypeSort = true;
  public ascScheduleNameSort = true;
  public ascServiceType = true;
  public ascScheduledDate = true;
  public ascCreatedDate = true;




  sort(sortValue){

   if(sortValue==='schedulerId') {
     this.ascScheduleNameSort = true;
     this.ascOrderTypeSort = true;
     this.ascServiceType = true;
     this.ascScheduledDate = true;
     this.ascCreatedDate = true;
     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort
     if(this.ascScheduleIdSort){
     this.ascScheduleIdSort = false;
     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort
     console.log(this.pageOfItems);
     }
     else{
     this.ascScheduleIdSort = true;
     this.pageOfItems.sort((a, b) => a.schedulerId - b.schedulerId); // For ascending sort
     console.log(this.pageOfItems);
         }


   } else if(sortValue==='orderType'){
     this.ascScheduleIdSort = true;
     this.ascScheduleNameSort = true;
     this.ascServiceType = true;
     this.ascScheduledDate = true;
     this.ascCreatedDate = true;
     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort

     if(this.ascOrderTypeSort){
       this.ascOrderTypeSort = false;
       this.pageOfItems.sort((a, b) => b.orderType > a.orderType ? -1:1); // For descending sort
       console.log(this.pageOfItems);
       }
       else{
       this.ascOrderTypeSort = true;
       this.pageOfItems.sort((a, b) => a.orderType > b.orderType ? -1:1); // For ascending sort
       console.log(this.pageOfItems);
 }

   }else if(sortValue==='serviceType')
   {
     this.ascScheduleIdSort = true;
     this.ascOrderTypeSort = true;
     this.ascScheduleNameSort = true;
     this.ascScheduledDate = true;
     this.ascCreatedDate = true;
     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort
     if(this.ascServiceType){
       this.ascServiceType = false;
       this.pageOfItems.sort((a, b) => b.serviceType > a.serviceType ? -1:1); // For descending sort
       console.log(this.pageOfItems);
       }
       else{
       this.ascServiceType = true;
       this.pageOfItems.sort((a, b) => a.serviceType > b.serviceType ? -1:1); // For ascending sort
       console.log(this.pageOfItems);
 }

   }
   else if(sortValue==='scheduleTime')
   {
     this.ascScheduleIdSort = true;
     this.ascScheduleNameSort = true;
     this.ascOrderTypeSort = true;
     this.ascServiceType = true;
     this.ascCreatedDate = true;
     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort
     if(this.ascScheduledDate){
       this.ascScheduledDate = false;
      // For descending sort
       this.pageOfItems.sort((val1, val2) => {
        return new Date(val2.nextRun).valueOf() - new Date(val1.nextRun).valueOf();
      });
       console.log(this.pageOfItems);
       }
       else{
       this.ascScheduledDate = true;
       // For ascending sort
       this.pageOfItems.sort((val1, val2) => {
        return new Date(val1.nextRun).valueOf() - new Date(val2.nextRun).valueOf();
      });
       console.log(this.pageOfItems);
 }

   }
   else if(sortValue==='scheduleName')
   {



     this.ascScheduleIdSort = true;
     this.ascOrderTypeSort = true;
     this.ascServiceType = true;
     this.ascScheduledDate = true;
     this.ascCreatedDate = true;



     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort
     if(this.ascScheduleNameSort){
       this.ascScheduleNameSort = false;

       this.pageOfItems.sort((a, b) => b.jobname > a.jobname ? -1:1 ); // For descending sort

       }
       else{
       this.ascScheduleNameSort = true;
       this.pageOfItems.sort((a, b) => a.jobname > b.jobname ? -1:1); // For ascending sort
       console.log(this.pageOfItems);
 }

   }

   else{

     this.ascScheduleIdSort = true;
     this.ascScheduleNameSort = true;
     this.ascOrderTypeSort = true;
     this.ascServiceType = true;
     this.ascScheduledDate = true;
     this.pageOfItems.sort((a, b) => b.schedulerId - a.schedulerId); // For descending sort

 if(this.ascCreatedDate){
   this.ascCreatedDate = false;
   // For descending sort
   this.pageOfItems.sort((val1, val2) => {
     return new Date(val2.jobCreatedAt).valueOf() - new Date(val1.jobCreatedAt).valueOf();
   });
   console.log(this.pageOfItems);
   }
   else{
   this.ascCreatedDate = true;
   this.pageOfItems.sort((val1, val2) => {
    //  For ascending
     return new Date(val1.jobCreatedAt).valueOf() - new Date(val2.jobCreatedAt).valueOf();
   });
   console.log(this.pageOfItems);
}

   }



 }
}
