import { Injectable } from '@angular/core';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpResponse } from '@angular/common/http';
import { Observable, pipe } from 'rxjs';
import { tap } from 'rxjs/operators';
import { LoaderService } from './loader.service';
import {environment} from '../../environments/environment'
@Injectable({
  providedIn: 'root'
})
export class LoaderinterceptorService {

  constructor(private loaderService: LoaderService) { }
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const  urlarray=environment.apiendpoint
     const {url}=req
     const currentpath=url.split('/')
 
  // var isSpinnerneed=urlarray.includes([...currentpath].pop())
//  console.log("url")
  var isSpinnerneed=!(urlarray.some(x=>url.includes(x)))
    //  console.log("req,",isSpinnerneed,req.url,currentpath.pop(),url.includes(''))
 //this.showLoader()
 
 if(isSpinnerneed==true)
 {
  //  console.log("co")
   this.showLoader();
 
 }
      
     return next.handle(req).pipe(tap((event: HttpEvent<any>) => { 
       
       if (event instanceof HttpResponse) {
         this.onEnd();
       }
     },
       (err: any) => {
          this.onEnd();
     }));
   }
   private onEnd(): void {
     this.hideLoader();
   }
   private showLoader(): void {
     this.loaderService.show();
   }
   private hideLoader(): void {
     setTimeout(() => {
       /** spinner ends after 5 seconds */
       this.loaderService.hide();
     }, 1000);
   }
}
