import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from './../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class LoginService {
  public url8080 = environment.serviceUrl;
  public headers : any;
  public ldapurllogin=environment.ldaplogin;

  constructor(private http: HttpClient) { 
    this.headers = new HttpHeaders();
  }

  /* ///////// Login ///////// */
  public login(data) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let params = data;
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json')
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));
    
    return this.http.post(this.url8080 +'/login/'+this.ldapurllogin, params, {headers:head}).pipe(map(val => {
      return val;
    })
    );
  }

  /* ///////// SignUp ///////// */
  // public signup(data) {
  //   const httpOptions = {
  //     headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
  //   };
  //   let params = data;
  //   return this.http.post('http://10.168.155.61:8080/login/register', params, httpOptions).pipe(map(val => {
  //     return val;
  //   })
  //   );
  // }
  public signup(data) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let params = data;
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json')
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));
    return this.http.post(this.url8080 + '/login/register', params, {headers:head}).pipe(map(val => {
      return val;
    })
    );
  }

  public changePassword(data, username) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let params = data;
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json')
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));
    return this.http.put(this.url8080 + '/login/updateUser/' + username, params, {headers:head}).pipe(map(val => {
      return val;
    })
    );
  }

  public getAllUsers() {
    let head= this.headers;
    // head = head.append('Cache-Control', 'no-cache');
    // head = head.append('Content-Type', 'application/json')
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));
    return this.http.get(this.url8080 + '/login/getUsersList',{headers:head});
  }

  /* ///////// Mail Send ///////// */
  public sendMail(data){
    const httpOptions = {
      headers: new HttpHeaders({  'Content-Type':  'application/json', 'Cache-Control': 'no-cache' })
    };
    let params =   data ;
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json')
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));
    return this.http.post(this.url8080 + '/mail/send', params, {headers:head} ).pipe(map(val => {
        return val;
      })
    );
}
}
