/*****Cost Weitage calculation*******/
costWeitageCalEdit(event:number,botIndex:number,suitIndex:number){
  // console.log(event,botIndex,suitIndex);
  this.sum=0;
  if(botIndex == -1){
    for(var k=0; k<this.costWeitageEdit[suitIndex].length; k++){
      this.costWeitageEdit[suitIndex][k] = event;
    }
  }
  for(var i=0; i<=suitIndex; i++){
    if(!this.costWeitageEdit[suitIndex]){
      this.costWeitageEdit[suitIndex]=[];
    }
    for(var j=0; j<=botIndex; j++){
      this.costWeitageEdit[suitIndex][botIndex] = event;
    }
    for (var i = 0; i < this.costWeitageEdit.length; i++) {
       for (var j = 0; j < this.costWeitageEdit[i].length; j++) {
        if(this.costWeitageEdit[i][j]){
          this.sum += Number(this.costWeitageEdit[i][j]);
        }
      }
    }
  }
  this.enterdCostSumEdit = this.sum;
  console.log(this.sum);
  if(this.sum == 100){
    this.hideCostWeitageEdit = true;
    this.errorValueEdit = 0;
    this.errorAlertEdit = false
  }
  else if(this.sum > 100){
    this.hideCostWeitageEdit = true;
    let diff = this.sum - 100;
    this.errorAlertEdit = true;
    this.errorValueEdit = event - diff;
  }
  else{
    this.hideCostWeitageEdit = false;
    this.errorValueEdit = 0;
    this.errorAlertEdit = false
  }
}

