/****AUTOMATION EDIT******/
updateAutomate(intake,index){
  this.submitted = true;
  this.alertIndex = index;
  if(this.automationFormEdit.invalid || this.errorValueEdit!=0 || this.enterdCostSumEdit >100 || this.enterdCostSumEdit == 0){
    this.alertdata = true;
    this.alertMessageModal = [{
      "type": "danger", "close": true,
      "content": "<strong>Kindly provide correct Automation Details</strong>",
    }
    ];
    setTimeout(function () { this.alertdata = false; }.bind(this), 5000);
    return;
  }else if(this.enterdCostSumEdit < 100){
    this.alertdata = true;
    this.alertMessageModal = [{
      "type": "danger", "close": true,
      "content": "<strong>Kindly provide '100%' saving!</strong>",
    }
    ];
    setTimeout(function () { this.alertdata = false; }.bind(this), 5000);
    return;
  }
  else{
    let pojo = {};
  pojo['intakeId'] = intake.intakeId;
  var suiteCatalogueEdit = [];
  for(var i=0; i<this.botcatalogueEdit().controls.length; i++){
    let suits={};
    suits['orderType'] = this.orderTypeEdit;
    suits['suiteName'] = this.botcatalogueEdit().controls[i].value.suiteNameEdit;
    // let role = {};
    // role['roleId'] = this.intake.team.ldapRole.roleId;
    // suits['role'] = role;
    suits['botCatalogue'] = [];
    for(var j=0; j<this.botDetailsEdit(i).controls.length; j++){
      let bot={};
      let val;
      try{
        val = JSON.parse(this.botDetailsEdit(i).controls[j].value.botInputEdit);
      }catch(e){
        this.isJsonError = true;
            this.alertdata = true;
            this.alertMessageModal = [{
              "type": "danger", "close": true,
              "content": "Error while parsing JSON Input on <strong>" + this.botDetailsEdit(i).controls[j].value.botNameEdit + " </strong>",
            }];
            setTimeout(function () { this.alertdata = false; }.bind(this), 7000);
            return;
      }
      bot['orderType'] = this.orderTypeEdit;
      var input={};
      input['input']=val;
      bot['botInput'] = input;
      bot['botName'] = this.botDetailsEdit(i).controls[j].value.botNameEdit;
      // bot['serviceType'] = this.botDetailsEdit(i).controls[j].value.serviceTypeEdit;


      if(this.botDetailsEdit(i).controls[j].value.botweightageEdit){
        bot['savingPercentage'] = this.botDetailsEdit(i).controls[j].value.botweightageEdit;
      }else{
        bot['savingPercentage'] = '0';
      }
      console.log(bot);
      suits['botCatalogue'].push(bot);
    }
    suiteCatalogueEdit.push(suits);
    pojo['suiteCatalogues'] = suiteCatalogueEdit;
  }
    console.log(pojo);
    // pojo = JSON.stringify(pojo);
    this.intakeService.automateIntakeEdit(pojo).subscribe(val=>{
      let response: any = val;
      console.log(response);
      if(response.message =="Intake Automated Successfully ,but Failed to update Dashboard!" || response.message =="Intake Automated Successfully!"){
        this.alertdata = true;
            this.alertMessageModal = [{
              "type": "success", "close": true,
              "content": "<strong>"+response.message+"</strong>",
            }];
            setTimeout(function () {
            this.alertdata = false;
              this.resetform();
              let element: HTMLElement = document.getElementById('btnModalCloseEdit') as HTMLElement;
              element.click();
              window.location.reload();
            }.bind(this), 5000);
      }else{
        this.alertdata = true;
        console.log(response.message)
        this.alertMessageModal = [{
          "type": "danger", "close": true,
          "content": "<strong>"+response.message+"</strong>",
        }];
        setTimeout(function () {
          this.alertdata = false;
        }.bind(this), 5000);
      }
    }, error => {
        let errorResponse = error;
    })
  }
}

