import { Component, OnInit } from '@angular/core';
import { environment } from './../../../../environments/environment';
import { IntakeService } from '../../../service/intake.service';

@Component({
  selector: 'app-productivity-dashboard',
  templateUrl: './productivity-dashboard.component.html',
  styleUrls: ['./productivity-dashboard.component.scss']
})
export class ProductivityDashboardComponent implements OnInit {
  public userDashboardStart = environment.userDashboardStart;
  public userDashboardEnd = environment.userDashboardEnd;
  public adminUserDashboard = environment.adminUserDashboard;
  public deptDashboardStart = environment.deptDashboardStart;
  public deptDashboardEnd = environment.deptDashboardEnd;
  public adminDeptDashboard = environment.adminDeptDashboard;
  public executiveDashboardStart = environment.executiveDashboardStart;
  public executiveDashboardEnd = environment.executiveDashboardEnd;
  public adminExecutiveDashboard = environment.adminExecutiveDashboard;
  // public deptDashboard = environment.deptDashboard;
  // public executiveDashboard = environment.executiveDashboard;
  menuList: any;
  dashboardUser = false;
  dashboardExecutive = false;
  dashboardDept = false;
  adminFlag = false;
  approverFlag=false;
  roles: any;
  user: any;
  userRoleMapping: any;
  deptFlag = false;
  execFlag = false;
  userFlag = false;

  constructor(private intakeService: IntakeService) { }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('USER'));
    this.menuList = JSON.parse(localStorage.getItem('MENU'));
    this.menuList = this.menuList.filter(item => item.mainMenu.menuName === 'Dashboard');
    this.menuList = this.menuList[0].subMenu;
    this.roles = JSON.parse(localStorage.getItem('ROLES'));
    this.intakeService.getUserRoleMapping(this.user.id).subscribe(
      val6 => {
        this.userRoleMapping = val6;
      });
    let r = this.roles.filter(item => item.rolePermission.permission === 'Admin');
    if (r.length > 0) {
      this.adminFlag = true;
    }
    let k = this.roles.filter(item => item.rolePermission.permission === 'Approver');
    if (k.length > 0) {
      this.approverFlag = true;
    }
    let j = this.menuList.filter(item => item.menuName === 'User Dashboard');
    if (j.length > 0) {
      this.dashboardUser = true;
      setTimeout(function () {
        if (this.adminFlag) {
          (<HTMLIFrameElement>document.getElementById('userDashboard')).src = this.adminUserDashboard;
        } else {
           let link = this.userDashboardStart + "query:(language:kuery,query:' user_name.keyword : "  + this.user.name;
          if (this.userRoleMapping.departmentHead) {
            link =link +" or department_head.keyword : " + this.user.name;
            this.userFlag = true;
          }
          if (this.userRoleMapping.subDepartmentHead) {
            link = link + " or sub_department_head.keyword : " + this.user.name;
            this.userFlag = true;
          }
          if (this.userRoleMapping.businessUnitHead) {
            link = link + " or business_head.keyword : " + this.user.name;
            this.userFlag = true;
          }
          if (this.userRoleMapping.subBusinessUnitHead) {
            link = link + " or sub_business_head.keyword : " + this.user.name;
            this.userFlag = true;
          }
          if (this.userRoleMapping.subDepartments && this.userRoleMapping.subDepartments !== null) {
            this.userRoleMapping.subDepartments.forEach((element, index) => {
              let encodedValue = encodeURIComponent("\""+element.teamName+"\"")
              link = link + " or sub_department.keyword : " + encodedValue;


                //link = link + " or sub_department.keyword : " + element.teamName;
            });
          }
          
          link = link + "')," + this.userDashboardEnd;
          // let link = this.userDashboardStart + "query:(language:kuery,query:'not Idea_name.keyword : Custom KPI and not Idea_name.keyword : Docsis Upstream Combiner Service Impairment Detection and not Idea_name.keyword : Jira testing and  user_name.keyword : " + this.user.name + " ')," + this.userDashboardEnd;
          (<HTMLIFrameElement>document.getElementById('userDashboard')).src = link
        }
        
      }.bind(this), 1000);
    }
    j = this.menuList.filter(item => item.menuName === 'Dept Dashboard');
    if (j.length > 0) {
      this.dashboardDept = true;
      setTimeout(function () {
        // (<HTMLIFrameElement>document.getElementById('deptDashboard')).src = this.deptDashboard;
        if (this.adminFlag || this.approverFlag) {
          (<HTMLIFrameElement>document.getElementById('deptDashboard')).src = this.adminDeptDashboard;
        } else {
          let link = this.deptDashboardStart + "query:(language:kuery,query:'"
          if (this.userRoleMapping.departmentHead) {
            link = link + "department_head.keyword : " + this.user.name;
            this.deptFlag = true;
          }
          if (this.userRoleMapping.subDepartmentHead) {
            if(this.deptFlag){
              link = link + " or ";
            }
            link = link + "sub_department_head.keyword : " + this.user.name;
            this.deptFlag = true;
          }
          if (this.userRoleMapping.businessUnitHead) {
            if(this.deptFlag){
              link = link + " or ";
            }
            link = link + "business_head.keyword : " + this.user.name;
            this.deptFlag = true;
          }
          if (this.userRoleMapping.subBusinessUnitHead) {
            if(this.deptFlag){
              link = link + " or ";
            }
            link = link + "sub_business_head.keyword : " + this.user.name;
            this.deptFlag = true;
          }
          link = link + "')," + this.deptDashboardEnd;
          // let link = this.deptDashboardStart + "query:(language:kuery,query:'not Idea_name.keyword : Custom KPI and not Idea_name.keyword : Docsis Upstream Combiner Service Impairment Detection and not Idea_name.keyword : Jira testing and  user_name.keyword : " + this.user.name + " ')," + this.deptDashboardEnd;
          (<HTMLIFrameElement>document.getElementById('deptDashboard')).src = link
        }
      }.bind(this), 1000);
    }
    j = this.menuList.filter(item => item.menuName === 'Executive Dashboard');
    if (j.length > 0) {
      this.dashboardExecutive = true;
      setTimeout(function () {
        // (<HTMLIFrameElement>document.getElementById('executiveDashboard')).src = this.executiveDashboard;
        if (this.adminFlag || this.approverFlag) {
          (<HTMLIFrameElement>document.getElementById('executiveDashboard')).src = this.adminExecutiveDashboard;
        } else {
          let link = this.executiveDashboardStart + "query:(language:kuery,query:'"
          if (this.userRoleMapping.departmentHead) {
            link = link + "department_head.keyword : " + this.user.name;
            this.execFlag = true;
          }
          if (this.userRoleMapping.subDepartmentHead) {
            if(this.execFlag){
              link = link + " or ";
            }
            link = link + "sub_department_head.keyword : " + this.user.name;
            this.execFlag = true;
          }
          if (this.userRoleMapping.businessUnitHead) {
            if(this.execFlag){
              link = link + " or ";
            }
            link = link + "business_head.keyword : " + this.user.name;
            this.execFlag = true;
          }
          if (this.userRoleMapping.subBusinessUnitHead) {
            if(this.execFlag){
              link = link + " or ";
            }
            link = link + "sub_business_head.keyword : " + this.user.name;
            this.execFlag = true;
          }
          link = link + "')," + this.executiveDashboardEnd;
          // let link = this.executiveDashboardStart + "query:(language:kuery,query:'not Idea_name.keyword : Custom KPI and not Idea_name.keyword : Docsis Upstream Combiner Service Impairment Detection and not Idea_name.keyword : Jira testing and  user_name.keyword : " + this.user.name + " ')," + this.executiveDashboardEnd;
          (<HTMLIFrameElement>document.getElementById('executiveDashboard')).src = link
        }
      }.bind(this), 1000);
    }
  }

}
