import { Component, OnInit } from '@angular/core';
// import { Component, OnInit, Input, Output, ElementRef, ViewChild, OnChanges, EventEmitter, SimpleChanges, SimpleChange } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from './../../service/login.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as CryptoJS from 'crypto-js';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss'],
})
export class SignupComponent implements OnInit {
  public message: any;
  public username: any = '';
  public password: any = '';
  public email: any = '';
  public name: any = '';
  public emailCheck: any = false;
  public usernameCheck: any = false;
  public confirmPassword: any = '';
  public alertMessage: any = [];
  public alertdata = false;
  registerForm: FormGroup;
  submitted = false;
  public invalidPassword: boolean;
  encryptionKey = "pr#daptR#gers@12";

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private formBuilder: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.registerForm = this.formBuilder.group({
      name: ['', Validators.required],
      username: ['', Validators.required],
      email: ['', [Validators.required, Validators.email, Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
      password: ['', [Validators.required, Validators.minLength(8)]],
      confirmPassword: ['', Validators.required]
    });
  }

  get f() { return this.registerForm.controls; }

  duplicateEmailCheck() {
    this.emailCheck = false;
  }

  passwordFails(checks: boolean[]): boolean {
    let counter = 0;
    for (let i = 0; i < checks.length; i++) {
      if (checks[i]) {
        counter += 1;
      }
    }
    return counter < 4;
  }


  validatePassword(password: string) {
    let hasLower = false;
    let hasUpper = false;
    let hasNum = false;
    let hasSpecial = false;

    const lowercaseRegex = new RegExp("(?=.*[a-z])");// has at least one lower case letter
    if (lowercaseRegex.test(password)) {
      hasLower = true;
    }

    const uppercaseRegex = new RegExp("(?=.*[A-Z])"); //has at least one upper case letter
    if (uppercaseRegex.test(password)) {
      hasUpper = true;
    }

    const numRegex = new RegExp("(?=.*\\d)"); // has at least one number
    if (numRegex.test(password)) {
      hasNum = true;
    }

    const specialcharRegex = new RegExp("[!@#$%^&*(),.?\":{}|<>]");
    if (specialcharRegex.test(password)) {
      hasSpecial = true;
    }
    this.invalidPassword = this.passwordFails([hasLower, hasUpper, hasNum, hasSpecial]);
  }

  duplicateUsernameCheck() {
    this.usernameCheck = false;
  }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
      return;
    }
    this.password = this.registerForm.value.password;
    this.username = this.registerForm.value.username;
    this.name = this.registerForm.value.name;
    this.email = this.registerForm.value.email;
    this.confirmPassword = this.registerForm.value.confirmPassword;
    console.log('input password', this.password.trim());
    let passwordd = CryptoJS.AES.encrypt(this.password.trim(), this.encryptionKey.trim()).toString();
    console.log('password', passwordd);
    if (this.password === this.confirmPassword) {
      let signUpData = {
        name: this.name,
        username: this.username,
        email: this.email,
        password: passwordd
      };
      this.loginService.signup(JSON.stringify(signUpData)).subscribe(
        val => {
          // this.message = val;
          // this.message = this.message.message;
          this.alertdata = true;
          this.alertMessage = [{
            "type": "success", "close": true,
            "content": "<strong>User registered successfully</strong>",
          }];
          setTimeout(function () {
            this.alertdata = false;
            // this.router.navigateByUrl('loginmain');
            window.location.reload();
          }.bind(this), 1000);
        },
        error => {
          this.message = error;
          this.message = this.message.error.message;
          // console.log('login', this.message);
          if (this.message.includes('Email')) {
            this.emailCheck = true;
          } else if (this.message.includes('Username')) {
            this.usernameCheck = true;
          } else {
            this.alertdata = true;
            this.alertMessage = [{
              "type": "danger", "close": true,
              "content": "<strong>" + this.message + "</strong>",
            }
            ];
            setTimeout(function () { this.alertdata = false; }.bind(this), 5000);
          }
        }
      );
    } else {
      return;
    }


  }
}
