import { Component, OnInit } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from '../../service/login.service';
import { IntakeService } from '../../service/intake.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-intake',
  templateUrl: './intake.component.html',
  styleUrls: ['./intake.component.scss'],
  providers: [DatePipe]
})
export class IntakeComponent implements OnInit {
  public message: any;
  public alertMessage: any = [];
  public alertdata = false;
  user: any;
  intakeCreate: FormGroup;
  intakeList: any = [];
  fullIntakeList: any = [];
  createdByMeList: any = [];
  approvalPendingList: any = [];
  pendingList: any = [];
  automatedCount: any;
  automationPendingList: any = [];
  pendingCount: any;
  draftCount: any;
  approvedCount: any;
  rejectedCount: any;
  totalCount: any;
  total = true;
  pending = false;
  draft = false;
  approved = false;
  rejected = false;
  automated = false;
  menuList: any;
  all = false;
  byMe = false;
  approval = false;
  automationTab = false;
  add = false;

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private intakeService: IntakeService,
    private formBuilder: FormBuilder,
    private datePipe: DatePipe,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('USER'));
    // console.log(this.user);
    if (!this.user) {
      this.router.navigateByUrl('loginmain');
    }

    this.menuList = JSON.parse(localStorage.getItem('MENU'));
    this.menuList = this.menuList.filter(item => item.mainMenu.menuName === 'Intake Portal');
    this.menuList = this.menuList[0].subMenu;
    let j = this.menuList.filter(item => item.menuName === 'All');
    if (j.length > 0) {
      this.all = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Uploaded By Me');
    if (j.length > 0) {
      this.byMe = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Add an Idea');
    if (j.length > 0) {
      this.add = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Approval Pending');
    if (j.length > 0) {
      this.approval = true;
    }
    j = this.menuList.filter(item => item.menuName === 'Automation Pending');
    if (j.length > 0) {
      this.automationTab = true;
    }

    this.intakeService.getAllIntakes().subscribe(
      val => {
        this.intakeList = val;
        this.intakeList.forEach((element, index) => {
          element.rfEffort.device = JSON.parse(element.rfEffort.device);
          element.rfEffort.application = JSON.parse(element.rfEffort.application);
        });
        this.fullIntakeList = val;
        this.fullIntakeList = this.fullIntakeList.filter(item => item.rfStatus.status !== 'DRAFT' || (item.rfStatus.status === 'DRAFT' && item.createdby.id === this.user.id));
        this.intakeList = this.intakeList.filter(item => item.rfStatus.status !== 'DRAFT' || (item.rfStatus.status === 'DRAFT' && item.createdby.id === this.user.id));
        this.totalCount = this.intakeList.length;
        // console.log('full', this.fullIntakeList);
        let j = this.intakeList.filter(item => item.rfStatus.status === 'PENDING');
        this.pendingCount = j.length;
        j = this.intakeList.filter(item => item.rfStatus.status === 'APPROVED');
        this.approvedCount = j.length;
        j = this.intakeList.filter(item => item.rfStatus.status === 'REJECTED');
        this.rejectedCount = j.length;
        j = this.intakeList.filter(item => item.rfStatus.status === 'DRAFT');
        this.draftCount = j.length;
        j = this.intakeList.filter(item => item.rfStatus.status === 'AUTOMATED');
        this.automatedCount = j.length;
        this.createdByMeList = this.intakeList.filter(item => item.createdby.id === this.user.id);
        // this.approvalPendingList = this.intakeList.filter(item => item.approver.id === this.user.id);
        this.approvalPendingList = this.approvalPendingList.filter(item => item.rfStatus.status === 'PENDING');
        this.pendingList = this.intakeList.filter(item => item.rfStatus.status === 'PENDING');
        this.automationPendingList = this.intakeList.filter(item => item.rfStatus.status === 'APPROVED');
      });
  }

  filterBasedonStatus(status) {
    if (status === 'total') {
      this.total = true;
      this.draft = false;
      this.approved = false;
      this.rejected = false;
      this.pending = false;
      this.automated = false;
      this.intakeList = JSON.parse(JSON.stringify(this.fullIntakeList));
      // this.intakeList = this.intakeList.filter(item => item.createdby.id === this.user.id || (item.approver.id === this.user.id && item.rfStatus.status !== 'DRAFT'));
    } else if (status === 'draft') {
      this.draft = true;
      this.total = false;
      this.approved = false;
      this.rejected = false;
      this.pending = false;
      this.automated = false;
      this.intakeList = JSON.parse(JSON.stringify(this.fullIntakeList.filter(item => item.rfStatus.status === 'DRAFT')));
    } else if (status === 'pending') {
      this.pending = true;
      this.total = false;
      this.approved = false;
      this.rejected = false;
      this.draft = false;
      this.automated = false;
      this.intakeList = JSON.parse(JSON.stringify(this.fullIntakeList.filter(item => item.rfStatus.status === 'PENDING')));
    } else if (status === 'approved') {
      this.approved = true;
      this.total = false;
      this.draft = false;
      this.rejected = false;
      this.pending = false;
      this.automated = false;
      this.intakeList = JSON.parse(JSON.stringify(this.fullIntakeList.filter(item => item.rfStatus.status === 'APPROVED')));
    } else if (status === 'rejected') {
      this.rejected = true;
      this.total = false;
      this.approved = false;
      this.draft = false;
      this.pending = false;
      this.automated = false;
      this.intakeList = JSON.parse(JSON.stringify(this.fullIntakeList.filter(item => item.rfStatus.status === 'REJECTED')));
    } else if (status === 'automated') {
      this.rejected = false;
      this.total = false;
      this.approved = false;
      this.draft = false;
      this.pending = false;
      this.automated = true;
      this.intakeList = JSON.parse(JSON.stringify(this.fullIntakeList.filter(item => item.rfStatus.status === 'AUTOMATED')));
    }
    this.createdByMeList = this.intakeList.filter(item => item.createdby.id === this.user.id);
    this.approvalPendingList = this.intakeList.filter(item => item.approver.id === this.user.id);
    this.approvalPendingList = this.approvalPendingList.filter(item => item.rfStatus.status === 'PENDING');
    this.pendingList = this.intakeList.filter(item => item.rfStatus.status === 'PENDING');
    this.automationPendingList = this.intakeList.filter(item => item.rfStatus.status === 'APPROVED');
  }
}
