import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { __importDefault } from 'tslib';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AutomationFactoryService } from 'src/app/service/automation-factory.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-scheduled-job-edit',
  templateUrl: './scheduled-job-edit.component.html',
  styleUrls: ['./scheduled-job-edit.component.scss']
})
export class ScheduledJobEditComponent implements OnInit {
  @Input() schedularDetails:any;
  @Input() index: number;
  @Input() activeId:any;
  public scheduleFormEdit: any;
  public active:any;
  public jobAlreadyExitsEdit = false;
  public minutes_minsEdit:any = [];
  public minuteScheduleFormEdit:any;
  public hoursScheduleFormEdit:any;
  public hourlyRadioEdit:any;
  public ShowFilterEdit= false
  public hours_hrsEdit:any = [];
  public hoursEdit: any = [];
  public hoursEndRangeEdit:any =[];
  public dailyScheduleFormEdit:any;
  public dailyRadioEdit:any;
  public daysEndRangeEdit:any;

  hoursMultiSelectSettingsEdit: any = {
    singleSelection: false,
    idField: '',
    textField: '',
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    itemsShowLimit: 2,
    limitSelection: 24,
    allowSearchFilter: this.ShowFilterEdit
  };
  daysMultiSelectSettingsEdit: any = {
    singleSelection: false,
    idField: '',
    textField: '',
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    itemsShowLimit: 2,
    limitSelection: 24,
    allowSearchFilter: this.ShowFilterEdit
  };

  public submitted = false;
  public alertdata = false;
  public alertMessage:any;
  public hoursMultiSelectEdit:any=[];
  public hoursFromEdit:any= "*";
  public hoursToEdit:any ="*";
  public everyHoursEdit = "*";
  public daysEdit=[];
  public minutesEdit:any =[];
  public weeklyScheduleFormEdit: any;
  public daysOfWeekEdit:any =[];
  public everyMinuteEdit:any = "*";
  public daysMultiSelectEdit:any = [];
  public daysFromEdit:any="*";
  public daysToEdit:any ="*";
  public daysOfWeekArray:any =[];
  public monthlyScheduleFormEdit:any;
  public monthlyRadioEdit:any;
  public monthsEdit =[];
  public monthssecondEdit:any;
  public monthlyDayofWeekEdit:any;
  public weeksEdit:any;
  public monthlyDaysEdit:any;
  public monthsFormEdit:any;
  public jobAlreadyExits:any;
  public dailyMinutesEdit:any;
  public dailyHoursEdit:any;
  public weeklyHoursEdit:any = '*';
  public weeklyMinutesEdit:any= '*';
  public monthlyMinutesEdit:any="*";
  public monthlyHoursEdit:any="*";
  public user:any;
  public roles:any;
  
  constructor(
    private automationService: AutomationFactoryService,
    private router: Router
    ) { 
    for (let i = 0; i < 60; i++) {
      this.minutes_minsEdit.push(i);
    }
    for (let i = 1; i < 24; i++) {
      this.hours_hrsEdit.push(i);
    }
    for (let i = 0; i < 24; i++) {
      this.hoursEdit.push(i);
    }
    for (let i = 1; i <= 31; i++) {
      this.daysEdit.push(i);
    }
    for (let i = 0; i < 60; i++) {
      this.minutesEdit.push(i);
    }
    for (let i = 1; i <= 12; i++) {
      this.monthsEdit.push(i);
    }
  }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('USER'));
    this.roles = JSON.parse(localStorage.getItem('ROLES'));
    this.jobAlreadyExitsEdit = false;
    this.active =this.schedularDetails.schedulerId;
    this.daysMultiSelectEdit = [];
  }
  ngOnChanges():void{
    // console.log(this.schedularDetails);
  /***********SCHEDULAR FORMS***************/  
    this.scheduleFormEdit = new FormGroup({
      'jobNameEdit': new FormControl(this.schedularDetails.jobname, Validators.required),
    });
  /**************HOURS FORMS**************/
  if(this.schedularDetails.hour != "*" && this.schedularDetails.minute == "0" && this.schedularDetails.day == "*" && this.schedularDetails.day_of_week == "?" && this.schedularDetails.month == "*"){
    let hours = this.schedularDetails.hour;
    if(hours.includes(',')){
      this.hoursMultiSelectEdit = JSON.parse("[" + hours + "]");
      (<HTMLInputElement>document.getElementById('hourlyOption1Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption2Edit')).checked = true;
      (<HTMLInputElement>document.getElementById('hourlyOption3Edit')).checked = false;
      this.hourlyRadioEdit = 'hourlyOption2Edit';  
    }else if(hours.includes('-')){
      var array = hours.split("-"); 
      this.hoursFromEdit = array[0];
      this.hoursToEdit = array[1];
      this.getHoursEndRangeEdit(this.hoursFromEdit);
      (<HTMLInputElement>document.getElementById('hourlyOption1Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption2Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption3Edit')).checked = true;
      this.hourlyRadioEdit = 'hourlyOption3Edit'; 
    }else{
      (<HTMLInputElement>document.getElementById('hourlyOption1Edit')).checked = true;
      (<HTMLInputElement>document.getElementById('hourlyOption2Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption3Edit')).checked = false;
      this.everyHoursEdit = hours.substring(2);
      this.hourlyRadioEdit = 'hourlyOption1Edit'; 
    }
  }else if(this.schedularDetails.hour == "*" && this.schedularDetails.minute != "*" && this.schedularDetails.day == "*" && this.schedularDetails.day_of_week == "?" && this.schedularDetails.month == "*"){
    this.everyMinuteEdit = this.schedularDetails.minute;
  }else if(this.schedularDetails.hour != "*" && this.schedularDetails.minute != "*"){
    if(this.schedularDetails.day == "*" && this.schedularDetails.day_of_week == "?" && this.schedularDetails.month == "*"){
      (<HTMLInputElement>document.getElementById('dailyOption1Edit')).checked = true; 
      // this.changeDailyRadioEdit('dailyOption1Edit');
      this.dailyRadioEdit = 'dailyOption1Edit';
      this.dailyHoursEdit = this.schedularDetails.hour;
      this.dailyMinutesEdit = this.schedularDetails.minute;
    }
    else if(this.schedularDetails.day != "*" && this.schedularDetails.day_of_week == "?" && this.schedularDetails.month == "*"){
      if(this.schedularDetails.day.includes(',')){
        (<HTMLInputElement>document.getElementById('dailyOption2Edit')).checked = true; 
        // this.changeDailyRadioEdit('dailyOption2Edit');
        this.daysMultiSelectEdit = JSON.parse("[" + this.schedularDetails.day + "]");
        this.dailyRadioEdit = 'dailyOption2Edit';
        this.dailyHoursEdit = this.schedularDetails.hour;
        this.dailyMinutesEdit = this.schedularDetails.minute;
      }else if(this.schedularDetails.day.includes('-')){
        (<HTMLInputElement>document.getElementById('dailyOption3Edit')).checked = true; 
        // this.changeDailyRadioEdit('dailyOption3Edit');
        let array = this.schedularDetails.day.split("-"); 
        this.daysFromEdit = array[0];
        this.daysToEdit = array[1];
        this.getDaysEndRangeEdit(this.daysFromEdit);
        this.dailyRadioEdit = 'dailyOption3Edit';
        this.dailyHoursEdit = this.schedularDetails.hour;
        this.dailyMinutesEdit = this.schedularDetails.minute;
      }else{
        (<HTMLInputElement>document.getElementById('dailyOption2Edit')).checked = true; 
        // this.changeDailyRadioEdit('dailyOption2Edit');
        this.dailyRadioEdit = 'dailyOption2Edit';
        this.daysMultiSelectEdit = JSON.parse("[" + this.schedularDetails.day + "]");
        this.dailyHoursEdit = this.schedularDetails.hour;
        this.dailyMinutesEdit = this.schedularDetails.minute;
      }
      
    }
    else if(this.schedularDetails.day == "?" && this.schedularDetails.day_of_week != "?" && this.schedularDetails.month == "*"){
      if(this.schedularDetails.day_of_week.includes(',')){
        this.daysOfWeekArray = JSON.parse("[" + this.schedularDetails.day_of_week + "]");
      }
      else{
        this.daysOfWeekArray.push(this.schedularDetails.day_of_week);
      }
      this.daysOfWeekArray.forEach(element => {
        if(element == "MON"){
          (<HTMLInputElement>document.getElementById('mondayEdit')).checked = true;
        }
        else if(element == "TUE"){
          (<HTMLInputElement>document.getElementById('tuesdayEdit')).checked = true;
        }
        else if(element == "WED"){
          (<HTMLInputElement>document.getElementById('wednesdayEdit')).checked = true;
        }
        else if(element == "THUR"){
          (<HTMLInputElement>document.getElementById('thursdayEdit')).checked = true;
        }
        else if(element == "FRI"){
          (<HTMLInputElement>document.getElementById('fridayEdit')).checked = true;
        }
        else if(element == "SAT"){
          (<HTMLInputElement>document.getElementById('saturdayEdit')).checked = true;
        }
        else if(element == "SUN"){
          (<HTMLInputElement>document.getElementById('sundayEdit')).checked = true;
        }
      });
      this.weeklyHoursEdit = this.schedularDetails.hour;
      this.weeklyMinutesEdit = this.schedularDetails.minute;
    }
    else if(this.schedularDetails.day == "?" && this.schedularDetails.day_of_week != "?" && this.schedularDetails.month != "*"){
      this.monthssecondEdit = this.schedularDetails.month;
      let array = this.schedularDetails.day_of_week.split("#"); 
        this.monthlyDayofWeekEdit = array[0];
        this.weeksEdit = array[1];
        this.monthlyHoursEdit = this.schedularDetails.hour;
      this.monthlyMinutesEdit = this.schedularDetails.minute;
    }
    else if(this.schedularDetails.day != "?" && this.schedularDetails.day != "*" && this.schedularDetails.day_of_week == "?" && this.schedularDetails.month != "*"){
      this.monthlyDaysEdit = this.schedularDetails.day;
      this.monthsFormEdit = this.schedularDetails.month;
      this.monthlyHoursEdit = this.schedularDetails.hour;
      this.monthlyMinutesEdit = this.schedularDetails.minute;
    }
    else{
      console.log("Unhandled case with both minutes and hours");
    }
  }
  else{
    console.log("Unhandled case");
  }

  /************MINUTES FORMS***************/
  this.minuteScheduleFormEdit = new FormGroup({
    'minuteEdit': new FormControl(this.everyMinuteEdit, Validators.required),
  });

  this.hoursScheduleFormEdit = new FormGroup({
    'everyHoursEdit': new FormControl("*/"+this.everyHoursEdit, Validators.required),
    'hoursMultiSelectEdit': new FormControl(this.hoursMultiSelectEdit, Validators.required),
    'hoursFromEdit': new FormControl(this.hoursFromEdit, Validators.required),
    'hoursToEdit': new FormControl(this.hoursToEdit, Validators.required),
  });

  this.dailyScheduleFormEdit = new FormGroup({
    'daysMultiSelectEdit': new FormControl(this.daysMultiSelectEdit, Validators.required),
    'daysFromEdit': new FormControl(this.daysFromEdit, Validators.required),
    'daysToEdit': new FormControl(this.daysToEdit, Validators.required),
    'dailyHoursEdit': new FormControl(this.dailyHoursEdit, Validators.required),
    'dailyMinutesEdit': new FormControl(this.dailyMinutesEdit, Validators.required),
  });

  this.weeklyScheduleFormEdit = new FormGroup({
    'weeklyHoursEdit': new FormControl(this.weeklyHoursEdit, Validators.required),
    'weeklyMinutesEdit': new FormControl( this.weeklyMinutesEdit, Validators.required),
    'mondayEdit': new FormControl('', Validators.required),
    'tuesdayEdit': new FormControl('', Validators.required),
    'wednesdayEdit': new FormControl('', Validators.required),
    'thursdayEdit': new FormControl('', Validators.required),
    'fridayEdit': new FormControl('', Validators.required),
    'saturdayEdit': new FormControl('', Validators.required),
    'sundayEdit': new FormControl('', Validators.required),
  });

  this.monthlyScheduleFormEdit = new FormGroup({
    'monthlyDaysEdit': new FormControl(this.monthlyDaysEdit, Validators.required),
    'monthsEdit': new FormControl(this.monthsFormEdit, Validators.required),
    'weeksEdit': new FormControl(this.weeksEdit, Validators.required),
    'monthlyDayofWeekEdit': new FormControl(this.monthlyDayofWeekEdit, Validators.required),
    'monthssecondEdit': new FormControl(this.monthssecondEdit, Validators.required),
    'monthlyHoursEdit': new FormControl(this.monthlyHoursEdit, Validators.required),
    'monthlyMinutesEdit': new FormControl(this.monthlyMinutesEdit, Validators.required),
  });
  
}
  /***********SCHEDULAR FORM***************/  
  get w() {
    return this.scheduleFormEdit.controls;
  }
  get x() {
    return this.minuteScheduleFormEdit.controls;
  }
  get y() {
    return this.hoursScheduleFormEdit.controls;
  }
  get z() {
    return this.dailyScheduleFormEdit.controls;
  }
  get u() {
    return this.weeklyScheduleFormEdit.controls;
  }
  get m() {
    return this.monthlyScheduleFormEdit.controls;
  }

  scheduleNameCheckEdit(value){
    this.scheduleFormEdit.patchValue({
    jobName: value.trim(),
  });
  }
  changeHoursRadioEdit(event: any) {
    this.hoursScheduleFormEdit.reset();
    (<HTMLInputElement>document.getElementById(event.target.value)).checked = true;
    if(event.target.value == 'hourlyOption2Edit'){
      (<HTMLInputElement>document.getElementById('hourlyOption1Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption3Edit')).checked = false;
    }
    if(event.target.value == 'hourlyOption1Edit'){
      (<HTMLInputElement>document.getElementById('hourlyOption2Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption3Edit')).checked = false;
    }
    if(event.target.value == 'hourlyOption3Edit'){
      (<HTMLInputElement>document.getElementById('hourlyOption2Edit')).checked = false;
      (<HTMLInputElement>document.getElementById('hourlyOption1Edit')).checked = false;
    }
    this.hourlyRadioEdit = event.target.value;
  }

  changeDailyRadioEdit(event: any) {
    this.dailyScheduleFormEdit.reset();
    (<HTMLInputElement>document.getElementById(event.target.value)).checked = true;
    this.dailyRadioEdit = event.target.value;
  }

  getHoursEndRangeEdit(hour) {
    this.hoursEndRangeEdit = [];
    if (hour === "") {
      this.hoursEndRangeEdit = [];
    } else {
      hour = hour * 1;
      for (let i = hour + 1; i < 24; i++) {
        this.hoursEndRangeEdit.push(i);
      }
    }
  }
  getDaysEndRangeEdit(day) {
    this.daysEndRangeEdit = [];
    if (day === "") {
      this.daysEndRangeEdit = [];
    } else {
      day = day * 1;
      for (let i = day + 1; i < 32; i++) {
        this.daysEndRangeEdit.push(i);
      }
    }
  }

  selectDaysOfWeekEdit(day, event: any) {
    if (event.target.checked === true) {
      this.daysOfWeekEdit.push(day);
    } else {
      let index = this.daysOfWeekEdit.indexOf(day);
      this.daysOfWeekEdit.splice(index, 1);
    }
    // console.log('daysofWeek', this.daysOfWeek);
  }

  changeMonthlyRadioEdit(event: any) {
    this.monthlyScheduleFormEdit.reset();
    (<HTMLInputElement>document.getElementById(event.target.value)).checked = true;
    this.monthlyRadioEdit = event.target.value;
  }

  toogleShowFilter() {
    this.ShowFilterEdit = !this.ShowFilterEdit;
    this.hoursMultiSelectSettingsEdit = Object.assign({}, this.hoursMultiSelectSettingsEdit, { allowSearchFilter: this.ShowFilterEdit });
  }
  /**********Update Form************/
  updateSchedular(i){
    this.submitted = true;
    if (this.scheduleFormEdit.invalid){
      this.alertdata = true;
      this.alertMessage = [{
        "type": "danger", "close": true,
        "content": "<strong> Kindly provide the Job details</strong>",
      }];
      setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
      return;
    }
    else if((<HTMLInputElement>document.getElementById('pills-min-'+i)).className.includes('active') && this.minuteScheduleFormEdit.invalid){
      this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
    }
    else if ((<HTMLInputElement>document.getElementById('pills-hour-'+i)).className.includes('active')) {
      if (this.hourlyRadioEdit === "hourlyOption1Edit" && this.y.everyHoursEdit.errors) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      }
      else if (this.hourlyRadioEdit === "hourlyOption2Edit" && this.y.hoursMultiSelectEdit.errors) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      }
      else if (this.hourlyRadioEdit === "hourlyOption3Edit" && (this.y.hoursFromEdit.errors || this.y.hoursToEdit.errors)) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      }
    } 
    else if ((<HTMLInputElement>document.getElementById('pills-daily-'+i)).className.includes('active')) {
      if (this.z.dailyHoursEdit.errors || this.z.dailyMinutesEdit.errors) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      } else if (this.dailyRadioEdit === "dailyOption2Edit" && this.z.daysMultiSelectEdit.errors) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      } else if (this.dailyRadioEdit === "dailyOption3Edit" && (this.z.daysFromEdit.errors || this.z.daysToEdit.errors)) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      }
    } 
    else if ((<HTMLInputElement>document.getElementById('pills-weekly-'+i)).className.includes('active') && (this.daysOfWeekEdit.length == 0 || (this.u.weeklyHoursEdit.errors || this.u.weeklyMinutesEdit.errors))) {
      this.alertdata = true;
      this.alertMessage = [{
        "type": "danger", "close": true,
        "content": "<strong> Kindly provide the Job details</strong>",
      }
      ];
      setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
      return;
    } 
    else if ((<HTMLInputElement>document.getElementById('pills-monthly-'+i)).className.includes('active')) {
      if (this.m.monthlyHoursEdit.errors || this.m.monthlyMinutesEdit.errors) {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong> Kindly provide the Job details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        return;
      } else if (this.monthlyRadioEdit === "monthlyOption1Edit") {
      } else if (this.monthlyRadioEdit === "monthlyOption2Edit") {
      }
    }
    // else{
      this.jobAlreadyExits = false;
      let schedulePojo = {
        // confType: this.orderDetailForm.value.configType,
        schedulerId : this.schedularDetails.schedulerId,
        day: "*",
        day_of_week: "?",
        hour: "*",
        input: this.schedularDetails.botInfo,
        // input:{},
        jobStatus: "pending",
        jobname: this.scheduleFormEdit.value.jobName,
        minute: "*",
        month: "*",
        jobModifiedBy: this.user.name,
        // role: {
        //   roleId: Number(this.orderDetailFormEdit.value.role)
        // },
        // orderType: this.orderDetailForm.value.orderType,
        // schedulerCreatedBy: this.user.name,
        // serviceType: this.orderDetailForm.value.serviceType
      };
      if ((<HTMLInputElement>document.getElementById('pills-min-'+i)).className.includes('active')) {
        schedulePojo.minute = this.minuteScheduleFormEdit.value.minuteEdit;
      } else if ((<HTMLInputElement>document.getElementById('pills-hour-'+i)).className.includes('active')) {
        if (this.hourlyRadioEdit === "hourlyOption1Edit") {
          schedulePojo.hour = this.hoursScheduleFormEdit.value.everyHoursEdit;
        } else if (this.hourlyRadioEdit === "hourlyOption2Edit") {
          schedulePojo.hour = this.hoursScheduleFormEdit.value.hoursMultiSelectEdit.join(',');
        } else if (this.hourlyRadioEdit === "hourlyOption3Edit") {
          schedulePojo.hour = this.hoursScheduleFormEdit.value.hoursFromEdit + '-' + this.hoursScheduleFormEdit.value.hoursToEdit;
        }
      } else if ((<HTMLInputElement>document.getElementById('pills-daily-'+i)).className.includes('active')) {
        if (this.dailyRadioEdit === "dailyOption1Edit") {
          schedulePojo.day = "*";
        } else if (this.dailyRadioEdit === "dailyOption2Edit") {
          schedulePojo.day = this.dailyScheduleFormEdit.value.daysMultiSelectEdit.join(',');
        } else if (this.dailyRadioEdit === "dailyOption3Edit") {
          schedulePojo.day = this.dailyScheduleFormEdit.value.daysFromEdit + '-' + this.dailyScheduleFormEdit.value.daysToEdit;
        }
        schedulePojo.day_of_week = '?';
        schedulePojo.hour = this.dailyScheduleFormEdit.value.dailyHoursEdit;
        schedulePojo.minute = this.dailyScheduleFormEdit.value.dailyMinutesEdit;
      } else if ((<HTMLInputElement>document.getElementById('pills-weekly-'+i)).className.includes('active')) {
        schedulePojo.day_of_week = this.daysOfWeekEdit.join(',');
        schedulePojo.day = '?';
        schedulePojo.hour = this.weeklyScheduleFormEdit.value.weeklyHoursEdit;
        schedulePojo.minute = this.weeklyScheduleFormEdit.value.weeklyMinutesEdit;
      } else if ((<HTMLInputElement>document.getElementById('pills-monthly-'+i)).className.includes('active')) {
        if (this.monthlyRadioEdit === "monthlyOption1Edit") {
          schedulePojo.day = this.monthlyScheduleFormEdit.value.monthlyDaysEdit;
          schedulePojo.day_of_week = '?';
          schedulePojo.month = this.monthlyScheduleFormEdit.value.monthsEdit;
        } else if (this.monthlyRadioEdit === "monthlyOption2Edit") {
          schedulePojo.day = '?';
          schedulePojo.month = this.monthlyScheduleFormEdit.value.monthssecondEdit;
          schedulePojo.day_of_week = this.monthlyScheduleFormEdit.value.monthlyDayofWeekEdit + '#' + this.monthlyScheduleFormEdit.value.weeksEdit;
        }
        schedulePojo.hour = this.monthlyScheduleFormEdit.value.monthlyHoursEdit;
        schedulePojo.minute = this.monthlyScheduleFormEdit.value.monthlyMinutesEdit;
      }
      // console.log('schedulePojo', schedulePojo);
      // return false;
      this.automationService.scheduleOrderUpdate(schedulePojo).subscribe(val => {
        let response: any = val;
        // console.log('response', response);
        // this.createdOrderId = response.scheduler_id;
        this.alertdata = true;
        this.alertMessage = [{
          "type": "success", "close": true,
          "content": "<strong> Job #" + response.scheduler_id + " is scheduled successfully!!</strong>",
        }];
        setTimeout(function () {
          this.alertdata = false; 
          let element: HTMLElement = document.getElementById('team-scheduled-order-list-tab') as HTMLElement;
          element.click();
        }.bind(this), 2000);
      }, error => {
        let errorResponse = error;
        // console.log('error', errorResponse.error);
        if (errorResponse.error === "Job Name Already Exists") {
          this.alertdata = true;
          this.jobAlreadyExits = true;
          this.alertMessage = [{
            "type": "danger", "close": true,
            "content": "<strong>Job Name Already Exists</strong>",
          }
          ];
          setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        } else {
          this.alertdata = true;
          this.alertMessage = [{
            "type": "danger", "close": true,
            "content": "<strong> Failed to schedule order</strong>",
          }
          ];
          setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
        }
      });
    // }
  }

  
}