  constructor(private automationService: AutomationFactoryService) {
    for (let i = 0; i < 60; i++) {
      this.minutes.push(i);
    }
    for (let i = 0; i < 24; i++) {
      this.hours.push(i);
    }
    for (let i = 1; i < 60; i++) {
      this.minutes_mins.push(i);
    }
    for (let i = 1; i < 24; i++) {
      this.hours_hrs.push(i);
    }
    for (let i = 1; i <= 31; i++) {
      this.days.push(i);
    }
    for (let i = 1; i <= 12; i++) {
      this.months.push(i);
    }
  }

