  selectDaysOfWeek(day, event: any) {
    if (event.target.checked === true) {
      this.daysOfWeek.push(day);
    } else {
      let index = this.daysOfWeek.indexOf(day);
      this.daysOfWeek.splice(index, 1);
    }
    // console.log('daysofWeek', this.daysOfWeek);
  }

  changeHoursRadio(event: any) {
    this.hoursScheduleForm.reset();
    (<HTMLInputElement>document.getElementById(event.target.value)).checked = true;
    this.hourlyRadio = event.target.value;
  }

  changeDailyRadio(event: any) {
    this.dailyScheduleForm.reset();
    (<HTMLInputElement>document.getElementById(event.target.value)).checked = true;
    this.dailyRadio = event.target.value;
  }

  changeMonthlyRadio(event: any) {
    this.monthlyScheduleForm.reset();
    (<HTMLInputElement>document.getElementById(event.target.value)).checked = true;
    this.monthlyRadio = event.target.value;
  }

  getHoursEndRange(hour) {
    this.hoursEndRange = [];
    if (hour === "") {
      this.hoursEndRange = [];
    } else {
      hour = hour * 1;
      for (let i = hour + 1; i < 24; i++) {
        this.hoursEndRange.push(i);
      }
    }
  }

  getDaysEndRange(day) {
    this.daysEndRange = [];
    if (day === "") {
      this.daysEndRange = [];
    } else {
      day = day * 1;
      for (let i = day + 1; i < 32; i++) {
        this.daysEndRange.push(i);
      }
    }
  }

  toogleShowFilter() {
    this.ShowFilter = !this.ShowFilter;
    this.hoursMultiSelectSettings = Object.assign({}, this.hoursMultiSelectSettings, { allowSearchFilter: this.ShowFilter });
  }

  selectAllBots(event: any, suit: any) {
    this.selectedBotsConfigData = [];
    this.chosenBotName = "";
    this.chosenBot = [
      {
        botId: ''
      }
    ];
    this.scheduleChecked = false;
    let classnames = (<HTMLInputElement>document.getElementById('collapseThree')).className;
    (<HTMLInputElement>document.getElementById('collapseThree')).className = classnames.replace('show', '');
    // console.log(event.target.value);
    // console.log(suit);
    if (event.target.checked == true) {
      suit.Bots.forEach(element => {
        if (this.selectedBotIds.includes(element.BotId)) {
          console.log('');
        } else {
          this.selectedBotIds.push(element.BotId);
          let arr: any = {};
          arr['botName'] = element.BotName;
          arr['suitName'] = suit.SuiteName;
          arr['botId'] = element.BotId;
          arr['suiteId'] = suit.SuiteId;
          this.summaryCatalogue.push(arr);
          this.botIds.push(element.BotId);
          this.suitsIds.push(suit.SuiteId);
        }
      });
    } else {
      suit.Bots.forEach(element => {
        let index = this.selectedBotIds.indexOf(element.BotId);
        this.selectedBotIds.splice(index, 1);
        this.summaryCatalogue.splice(index, 1);
        this.botIds.splice(index, 1);
        this.suitsIds.splice(index, 1);
        // console.log(this.selectedBotIds);
      });
    }
  }

  scheduleJobCheck(event: any) {
    if (event.target.checked === true) {
      this.scheduleChecked = true;
    } else {
      this.scheduleChecked = false;
    }
  }

  botSelected(suit: any, bot: any, event: any) {
    this.selectedBotsConfigData = [];
    this.chosenBotName = "";
    this.chosenBot = [
      {
        botId: ''
      }
    ];
    this.scheduleChecked = false;
    let classnames = (<HTMLInputElement>document.getElementById('collapseThree')).className;
    (<HTMLInputElement>document.getElementById('collapseThree')).className = classnames.replace('show', '');
    // console.log(suit);
    // console.log(bot);
    if (event.target.checked == true) {
      this.selectedBotIds.push(bot.BotId);
      let arr: any = {};
      arr['botName'] = bot.BotName;
      arr['suitName'] = suit.SuiteName;
      arr['botId'] = bot.BotId;
      arr['suiteId'] = suit.SuiteId;
      this.summaryCatalogue.push(arr);
      this.botIds.push(bot.BotId);
      this.suitsIds.push(suit.SuiteId);
      // console.log("Selected Bots: " + this.selectedBotIds);
      // console.log("Selected Suits:" + this.suitsIds);
      // console.log("Selected BotsIds:" + this.botIds);
    } else {
      //this.selectedBotIds.findIndex(item => item === event.target.value);
      let index = this.selectedBotIds.indexOf(bot.BotId);
      this.selectedBotIds.splice(index, 1);
      this.summaryCatalogue.splice(index, 1);
      this.botIds.splice(index, 1);
      this.suitsIds.splice(index, 1);
      // console.log(this.selectedBotIds);
    }
    // console.log('summaryCatalogue', this.summaryCatalogue);
  }

  loadBotConfig() {
    if (this.selectedBots.length == 0) {
      this.automationService.getBotConfig(this.selectedBotIds).subscribe(val => {
        // console.log(val);
        this.selectedBotsConfigData = val;
      });
      this.selectedBotIds.forEach(element => {
        this.selectedBots.push(element);
      });
      this.selectedSuits = [];
      this.suitsIds.forEach(element => {
        this.selectedSuits.push(element);
      });
      this.summaryCatalogue.forEach(element => {
        this.summaryCatalogueFinal.push(element);
      });


    } else {
      // let indexToBespliced = [];
      this.selectedBots = this.selectedBots.filter(item => this.selectedBotIds.includes(item));
      this.automationService.getBotConfig(this.selectedBotIds).subscribe(val => {
        this.selectedBotsConfigData = val;
        // console.log(val);
      });
      this.selectedBotIds.forEach(element => {
        if (this.selectedBots.includes(element)) {
          // console.log('');
        } else {
          this.selectedBots.push(element);
        }

      });
      this.selectedSuits = [];
      this.suitsIds.forEach(element => {
        this.selectedSuits.push(element);
      });
      this.summaryCatalogueFinal = [];
      this.summaryCatalogue.forEach(element => {
        this.summaryCatalogueFinal.push(element);
      });
    }
    for (let i = 0; i < this.selectedBotsConfigData.length; i++) {
      this.selectedBotsConfigData[i].BotInput = JSON.parse(this.selectedBotsConfigData[i].BotInput);
    }
    if (this.selectedBotsConfigData.length == 0) {
      let classnames = (<HTMLInputElement>document.getElementById('collapseThree')).className;
      (<HTMLInputElement>document.getElementById('collapseThree')).className = classnames.replace('show', '');
      classnames = (<HTMLInputElement>document.getElementById('collapseFour')).className;
      (<HTMLInputElement>document.getElementById('collapseFour')).className = classnames.replace('show', '');
    }
  }

  showConfig(bot) {
    // console.log('bot', bot);
    // console.log('config', this.selectedBotsConfigData);
    this.chosenBot = this.selectedBotsConfigData.filter(item => item.botId === bot.botId);
    if (this.chosenBot.length > 0) {
      this.chosenBotName = bot.botName;
    }
    // (<HTMLSelectElement>document.getElementById("botConfig")).value = this.chosenBot[0].BotInput.input;
    // console.log('chosenBot', this.chosenBot);
  }
