import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from './../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AdminPortalService {
  public url8080 = environment.serviceUrl;
  public headers : any;


  constructor(private http: HttpClient) { 
    this.headers = new HttpHeaders();
  }

  public createServer(data) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json')
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));

    let params = data;
    return this.http.post(this.url8080 + '/login/updateserver', params, {headers:head}).pipe(map(val => {
      return val;
    })
    );
  }

  public getServerList(){
    let head= this.headers;
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));

    return this.http.get(this.url8080 + '/login/serverlist',{headers:head});
  }

  public getServerType(){
    let head= this.headers;
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));

    return this.http.get(this.url8080 + '/login/servertype',{headers:head});
  }
  
  public getServerDetailList(data){
    let head= this.headers;
    let auth = localStorage.getItem('basicAuth');
    head = head.append("Authorization", "Basic " + btoa(auth));

    return this.http.get(this.url8080 + '/login/serverbyid/'+data,{headers:head});
  }
}
