import { Component, OnInit, Input, Output, ElementRef, ViewChild, OnChanges, EventEmitter, SimpleChanges, SimpleChange } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from './../../../service/login.service';
import { IntakeService } from './../../../service/intake.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-intake-user-created',
  templateUrl: './intake-user-created.component.html',
  styleUrls: ['./intake-user-created.component.scss']
})
export class IntakeUserCreatedComponent implements OnInit {
  @Input() intakes: any;
  editData: any;
  index: any;
  viewData: any;
  viewindex: any;
  public message: any;
  public alertMessage: any = [];
  public alertdata = false;
  alertIndex: any;
  user: any;
  upadateJiraContent: any;

  constructor(
    public http: HttpClient,
    private intakeService: IntakeService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('USER'));
  }

  deleteIntake(intake, index) {
    this.alertIndex = index;
    this.intakeService.deleteIntake(intake.intakeId).subscribe(
      val => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "success", "close": true,
          "content": "Intake deleted successfully",
        }];
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
      },
      error => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong>Failed</strong> to delete intake",
        }
        ];
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
      });
  }

  editIntake(intake, index){
    // console.log(intake);
    this.editData = intake;
    this.index = index;
  }

  viewIntake(intake, index){
    this.viewData = intake;
    this.viewindex = index;
  }

  updateJira(intake, index) {
    this.alertdata = index;
    intake.rfEffort.device = JSON.stringify(intake.rfEffort.device);
    intake.rfEffort.application = JSON.stringify(intake.rfEffort.application);
    this.intakeService.jiraCreateAndUpdate(JSON.stringify(intake)).subscribe(
      val => {
        this.upadateJiraContent = val;
        this.alertdata = true;
        if (this.upadateJiraContent.message.includes('failed') || this.upadateJiraContent.message.includes('Failed')) {
          this.alertMessage = [{
            "type": "danger", "close": true,
            "content": this.upadateJiraContent.message,
          }];
        } else {
          this.alertMessage = [{
            "type": "success", "close": true,
            "content": this.upadateJiraContent.message,
          }];
        }
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
        // console.log(this.upadateJiraContent.message);
      });
  }
}
