  updateJira(intake, index) {
    this.alertdata = index;
    intake.rfEffort.device = JSON.stringify(intake.rfEffort.device);
    intake.rfEffort.application = JSON.stringify(intake.rfEffort.application);
    this.intakeService.jiraCreateAndUpdate(JSON.stringify(intake)).subscribe(
      val => {
        this.upadateJiraContent = val;
        this.alertdata = true;
        if (this.upadateJiraContent.message.includes('failed') || this.upadateJiraContent.message.includes('Failed')) {
          this.alertMessage = [{
            "type": "danger", "close": true,
            "content": this.upadateJiraContent.message,
          }];
        } else {
          this.alertMessage = [{
            "type": "success", "close": true,
            "content": this.upadateJiraContent.message,
          }];
        }
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
        // console.log(this.upadateJiraContent.message);
      });
  }

