import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LoginService } from '../../service/login.service';
import { AdminPortalService } from '../../service/admin-portal.service';
import { Router } from '@angular/router';
import { FormBuilder,FormControl, FormGroup, Validators } from '@angular/forms';
import * as CryptoJS from 'crypto-js';
import { Subscription, timer } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-admin-console',
  templateUrl: './admin-console.component.html',
  styleUrls: ['./admin-console.component.scss']
})
export class AdminConsoleComponent implements OnInit {
  public modalTitle:any;
  public serverList:any;
  public serverDetailUpadte:any;
  public serverDetailsForm:any;
  public serverDetailList:any;
  public submitted = false;
  public serverTypeList:any;
  public serverType:any;
  public serverName :any;
  public serverState = false;
  public serverStatus= 'Inactive';
  public alertdata = false;
  public alertMessage: any = [];
  public serverTypeName:any;
  public createdServerId:any;
  public serverDetailsFormEdit:any;
  public activeShow:any;
  public isActive= false;
  public serverTypeNameEdit:any;
  public serverNameEdit:any;
  public serverTypeEdit:any;
  public serverIdEdit:any;
  public submittedEdit:any;
  public passwordEdit:any;
  public serverTypeId:any;
  public serverTypeIdEdit:any;
  encryptionKey = "pr#daptR#gers@12";
  public password:any;
  pageOfItems: Array<any>;
  pageSize: any;
  public serverAlreadyExits:any;

  constructor(
    public http: HttpClient,
    private loginService: LoginService,
    private adminPortalService: AdminPortalService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.serverStatus = 'Active';
    this.serverState = true;
    this.serverType = {};
    this.pageSize = 10;
    this.serverType['typeId']= '';
    this.serverTypeName = '';
    this.serverTypeNameEdit = '';
    /*******GET ALL SERVER LIST***********/
    this.adminPortalService.getServerList().subscribe(val =>{
      this.serverList = val;
      this.serverList = this.serverList.data;
      console.log(this.serverList);
    })

    /***********GET SERVER TYPE LIST************/
    this.adminPortalService.getServerType().subscribe(val =>{
      this.serverTypeList = val;
      this.serverTypeList = this.serverTypeList.data;
      console.log("**************server Type************",this.serverTypeList);
    })

    /****CREATE A NEW FORM GROUP*****/
    this.serverDetailsForm = new FormGroup({
      'serverType': new FormControl('', Validators.required),
      'serverName': new FormControl('', Validators.required),
      'serverIp': new FormControl('', Validators.required),
      'bindDn': new FormControl('', Validators.required),
      'baseDn': new FormControl(''),
      'password': new FormControl('', Validators.required),
      'serverUrl': new FormControl('', Validators.required),
      'serverPort': new FormControl('', Validators.required),
    });
  }

   /*****FORM CONTROL*******/
   get v() {
    return this.serverDetailsForm.controls;
  }
  get m() {
    return this.serverDetailUpadte.controls;
  }

  /*******MODAL HEADER*********/
  getModal(modalName){
    this.modalTitle = modalName;
    console.log(this.modalTitle);
  }

  /*****GET SERVER-ID FROM SERVER TYPE******/
  getServerId(event: any){
    console.log(event);
    this.serverTypeList.forEach(element => {
      if (element.typeName == event) {
        this.serverName = element.typeName;
        this.serverTypeId = element.typeId;
      }
    });
    if(this.serverName == 'LDAP'){
      this.serverDetailsForm.controls['baseDn'].setValidators(Validators.required);
      this.serverDetailsForm.controls["baseDn"].updateValueAndValidity();
    }
    else{
      this.serverDetailsForm.controls['baseDn'].clearValidators(Validators.required);
      this.serverDetailsForm.controls["baseDn"].updateValueAndValidity();
    }
    this.serverType = {};
    this.serverType['typeId']= this.serverTypeId;
    this.serverTypeName = event;
    console.log(this.serverType);
  }
  /*****GET SERVER-ID FROM SERVER TYPE FOR EDIT******/
  getServerIdEdit(event: any){
    console.log(event);
    this.serverTypeList.forEach(element => {
      if (element.typeName == event) {
        this.serverNameEdit = element.typeName;
        this.serverTypeIdEdit = element.typeId;
      }
    });
    if(this.serverNameEdit == 'LDAP'){
      this.serverDetailUpadte.controls['baseDnEdit'].setValidators(Validators.required);
      this.serverDetailUpadte.controls["baseDnEdit"].updateValueAndValidity();
    }
    else{
      this.serverDetailUpadte.controls['baseDnEdit'].clearValidators(Validators.required);
      this.serverDetailUpadte.controls["baseDnEdit"].updateValueAndValidity();
    }
    this.serverTypeEdit = {};
    this.serverTypeEdit['typeId']= this.serverTypeIdEdit;
    this.serverTypeNameEdit = this.serverNameEdit;
  }

  /*****GET SERVER-STATUS FROM CHECKBOX******/
  activateServer(event: any){
    if (event.target.checked === true) {
      this.serverState = true;
      this.serverStatus = 'Active';
    } else {
      this.serverState = false;
      this.serverStatus = 'Inactive';
    }
  }
  /*****GET SERVER-STATUS FROM CHECKBOX FOR EDIT******/
  activateServerEdit(event: any){
    if (event.target.checked === true) {
      this.isActive = true;
      this.serverStatus = 'Active';
    } else {
      this.isActive = false;
      this.serverStatus = 'Inactive';
    }
  }

  /*******CREATE A NEW SERVER DETAILS*********/
  createNew(){
    this.submitted = true;
    console.log(this.serverDetailsForm);
    console.log(this.serverDetailsForm.controls);
    this.password = this.serverDetailsForm.value.password.trim();
    let passwordencrypted = CryptoJS.AES.encrypt(this.password.trim(), this.encryptionKey.trim()).toString();
    if (this.serverDetailsForm.invalid) {
      this.alertdata = true;
      this.alertMessage = [{
        "type": "danger", "close": true,
        "content": "<strong> Kindly provide the Server details</strong>",
      }
      ];
      setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
      return;
    } else {
      let pojo={};
      pojo['active'] = this.serverState;
      pojo['baseDn'] = this.serverDetailsForm.value.baseDn;
      pojo['bindDn'] = this.serverDetailsForm.value.bindDn;
      pojo['id'] = 0;
      pojo['password'] = passwordencrypted;
      pojo['serverIp'] = this.serverDetailsForm.value.serverIp;
      pojo['serverName'] = this.serverDetailsForm.value.serverName;
      pojo['serverPort'] = this.serverDetailsForm.value.serverPort;
      pojo['serverType'] = this.serverType;
      pojo['serverUrl'] = this.serverDetailsForm.value.serverUrl;
      // pojo['status'] = this.serverStatus;
      console.log(pojo);
      pojo = JSON.stringify(pojo);
      this.adminPortalService.createServer(pojo).subscribe(val=>{
        let response: any = val;
            this.createdServerId = response.data.serverName;
            if(response.message == "Another Server of this type is already active. Please make it inactive!"){
              this.alertdata = true;
              this.serverAlreadyExits = true;
              this.alertMessage = [{
                "type": "danger", "close": true,
                "content": "Another Server with HostName "+"<strong>'"+ this.createdServerId +"'</strong>" + " is already active. Please make it inactive!",
              }
              ];
              setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
            }
            else{
              this.alertdata = true;
              this.alertMessage = [{
                "type": "success", "close": true,
                "content": "<strong>Server created successfully!</strong>",
              }];
              setTimeout(function () {
                this.alertdata = false;
                this.resetform();
              }.bind(this), 1000);
            }    
      }, error => {
        let errorResponse = error;
     })
    }
    console.log(this.serverType);
  }

  /*********FETCH SERVER DETAILS BY ID*************/
  fetchDetails(event,id){
      let classnames = (<HTMLInputElement>document.getElementById('list'+id)).className;
      if(classnames.includes('collapsed')){
          this.adminPortalService.getServerDetailList(event).subscribe(val =>{
          this.serverDetailList = val;
          console.log(this.serverDetailList);
          this.serverDetailList = this.serverDetailList.data;
          this.serverIdEdit = this.serverDetailList.id;
          this.serverTypeEdit = {};
          this.serverTypeNameEdit = this.serverDetailList.serverType.typeName;
          console.log(this.serverNameEdit);
          this.serverTypeEdit['typeId']= this.serverDetailList.serverType.typeId;
          let decrypt = CryptoJS.AES.decrypt(this.serverDetailList.password.trim(), this.encryptionKey.trim()).toString(CryptoJS.enc.Utf8);
          console.log(this.serverDetailList.password);
          this.serverDetailUpadte = new FormGroup({
            serverNameEdit: new FormControl(this.serverDetailList.serverName, Validators.required),
            serverTypeEdit: new FormControl(this.serverDetailList.serverType.typeName, Validators.required),
            serverIpEdit: new FormControl(this.serverDetailList.serverIp, Validators.required),
            bindDnEdit: new FormControl(this.serverDetailList.bindDn, Validators.required),
            baseDnEdit: new FormControl(this.serverDetailList.baseDn),
            passwordEdit: new FormControl(decrypt, Validators.required),
            serverUrlEdit: new FormControl(this.serverDetailList.serverUrl, Validators.required),
            serverPortEdit: new FormControl(this.serverDetailList.serverPort, Validators.required),
          })
          this.activeShow = id;
          if(this.serverDetailList.active){
            this.isActive = true;
          }
          else{
            this.isActive = false;
          }
        });
      }
      else{
        this.activeShow = null;
        console.log(this.serverDetailList);
      }
  }

  /**********UPDATE THE SERVER DETAILS***************/
  updateDetails(){
    this.submittedEdit = true;
    this.passwordEdit = this.serverDetailUpadte.value.passwordEdit;
    let passwordencrypted = CryptoJS.AES.encrypt(this.passwordEdit.trim(), this.encryptionKey.trim()).toString();
    if (this.serverDetailUpadte.invalid) {
      this.alertdata = true;
      this.alertMessage = [{
        "type": "danger", "close": true,
        "content": "<strong> Kindly provide the Server details</strong>",
      }
      ];
      setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
      return;
    } else {
      let pojo={};
      pojo['active'] = this.isActive;
      pojo['baseDn'] = this.serverDetailUpadte.value.baseDnEdit;
      pojo['bindDn'] = this.serverDetailUpadte.value.bindDnEdit;
      pojo['id'] = this.serverIdEdit;
      pojo['password'] = passwordencrypted;
      pojo['serverIp'] = this.serverDetailUpadte.value.serverIpEdit;    
      pojo['serverName'] = this.serverDetailUpadte.value.serverNameEdit;
      pojo['serverPort'] = this.serverDetailUpadte.value.serverPortEdit;
      pojo['serverType'] = this.serverTypeEdit;
      pojo['serverUrl'] = this.serverDetailUpadte.value.serverUrlEdit;
      console.log(pojo);
      pojo = JSON.stringify(pojo);
      this.adminPortalService.createServer(pojo).subscribe(val =>{
        let response: any = val;
        this.createdServerId = response.data.serverName;
            if(response.message == "Another Server of this type is already active. Please make it inactive!"){
              this.alertdata = true;
              this.serverAlreadyExits = true;
              this.alertMessage = [{
                "type": "danger", "close": true,
                "content": "Another Server with HostName "+"<strong>'"+ this.createdServerId +"'</strong>" + " is already active. Please make it inactive!",
              }
              ];
              setTimeout(function () { this.alertdata = false;this.resetform(); }.bind(this), 3000);
            }
            else{
              this.alertdata = true;
              this.alertMessage = [{
                "type": "success", "close": true,
                "content": "<strong>Server Updated successfully!</strong>",
              }];
              setTimeout(function () {
                this.alertdata = false;
                this.resetform();
              }.bind(this), 1000);
            }   
      }, error => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong>Failed to update Server details</strong>",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 3000);
      })
    }
  }

  ngOnChanges(): void {
    this.pageSize = 10;
  }

  onChangePage(pageOfItems: Array<any>) {
    this.pageOfItems = pageOfItems;
  }

  /*****RESET FORM*******/
  resetform(){
    this.submitted = false;
    this.serverDetailsForm.reset();
    this.ngOnInit();
  }

}
