import { Component, OnInit, Input, Output, ElementRef, ViewChild, OnChanges, EventEmitter, SimpleChanges, SimpleChange } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginService } from './../../../service/login.service';
import { IntakeService } from './../../../service/intake.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-pending',
  templateUrl: './pending.component.html',
  styleUrls: ['./pending.component.scss']
})
export class PendingComponent implements OnInit {
  @Input() intakes: any;
  editData: any;
  index: any;
  viewData: any;
  viewindex: any;
  public message: any;
  public alertMessage: any = [];
  public alertdata = false;
  alertIndex: any;
  roles: any;
  approverFlag = false;

  constructor(
    public http: HttpClient,
    private intakeService: IntakeService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    // console.log('user', this.intakes);
    this.roles = JSON.parse(localStorage.getItem('ROLES'));
    let j = this.roles.filter(item => item.rolePermission.permission === 'Approver');
    if (j.length > 0) {
      this.approverFlag = true;
    }
  }

  deleteIntake(intake, index) {
    this.alertIndex = index;
    this.intakeService.deleteIntake(intake.intakeId).subscribe(
      val => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "success", "close": true,
          "content": "Intake deleted successfully",
        }];
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
      },
      error => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong>Failed</strong> to delete intake",
        }
        ];
        setTimeout(function () { this.alertdata = false; }.bind(this), 1000);
      });
  }

  editIntake(intake, index){
    // console.log(intake);
    this.editData = intake;
    this.index = index;
  }

  viewIntake(intake, index){
    this.viewData = intake;
    this.viewindex = index;
  }
}
