  onChangePage(pageOfItems: Array<any>) {
    this.pageOfItems = pageOfItems;
  }

  getBotDetails(bot) {
    this.selectedBot = bot;
  }

  refreshBotLog(status){
    let link = "";
    if (status === 'play'){
      this.play = false;
      this.pause = true;
      link = this.livelogStart + 'refreshInterval:(pause:!f,value:3000),' + this.livelogMid + "query:(language:kuery,query:'order_id:" + this.logorderId + "')," + this.livelogEnd;
    } else if (status === 'pause'){
      this.play = true;
      this.pause = false;
      link = this.livelogStart + 'refreshInterval:(pause:!t,value:0),' + this.livelogMid + "query:(language:kuery,query:'order_id:" + this.logorderId + "')," + this.livelogEnd;
    }
    (<HTMLIFrameElement>document.getElementById('botreport')).src = link;
  }

  botlogs(selectedOrder) {
    this.logorderId = selectedOrder.orderId;
    let link = "";
    if (selectedOrder.orderStatus === 'completed' || selectedOrder.orderStatus === 'Completed') {
      this.play = true;
      this.pause = false;
      link = this.livelogStart + 'refreshInterval:(pause:!t,value:0),' + this.livelogMid + "query:(language:kuery,query:'order_id:" + this.logorderId + "')," + this.livelogEnd;
    } else {
      this.pause = true;
      this.play = false;
      link = this.livelogStart + 'refreshInterval:(pause:!f,value:3000),' + this.livelogMid + "query:(language:kuery,query:'order_id:" + this.logorderId + "')," + this.livelogEnd;
    }
    (<HTMLIFrameElement>document.getElementById('botreport')).src = link;
  }

  automationlogs(orderId) {
    let link = this.serviceUrlRogers + "/order/viewReport/report.html?id="+ orderId;
    (<HTMLIFrameElement>document.getElementById('viewreport')).src = link;
  }

  botlogClosed() {
    let link = this.livelogStart + 'refreshInterval:(pause:!t,value:0),' + this.livelogMid + "query:(language:kuery,query:'order_id:" + this.logorderId + "')," + this.livelogEnd;
    (<HTMLIFrameElement>document.getElementById('botreport')).src = link;
  }

