import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from './../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AutomationFactoryService {
  public url8080 = environment.serviceUrlRogers;
  public url1 = environment.serviceUrl;
  public headers : any;

  static refreshlist = new EventEmitter<any>();

  constructor(private http: HttpClient) {
    this.headers = new HttpHeaders();
    let auth = localStorage.getItem('basicAuth');
    this.headers = this.headers.append("Authorization", "Basic " + btoa(auth));
    // this.headers = this.headers.append("Content-Type", "application/x-www-form-urlencoded");
   }

  // public getAllOrders() {
  //   return this.http.get(this.url8080 + '/order/getOrderList');
  // }

  public getAllOrders(roles) {
    let data = 'role=';
    roles.forEach((element, index) => {
      if (index == 0) {
        data = data + element.roleId;
      } else {
        data = data + ',' + element.roleId;
      }

    });
    return this.http.get(this.url8080 + '/order/getOrderList?' + data,{headers: this.headers});
  }

  public getOrderDetails(orderId) {
    return this.http.get(this.url8080 + '/order/getInfoByOrderId?orderId=' + orderId,{headers: this.headers});
  }

  public getCatalogueList() {
    return this.http.get(this.url8080 + '/order/getCatalogue',{headers: this.headers});
  }

  public getCatalogueByorderType(orderType, role){
    return this.http.get(this.url1 + '/order/getCatalogue?orderType=' + orderType + '&role=' + role,{headers: this.headers});
  }

  public getBotConfig(botId) {
    let data = 'botId=';
    botId.forEach((element, index) => {
      if (index == 0) {
        data = data + element;
      } else {
        data = data + ',' + element;
      }

    });
    return this.http.get(this.url1 + '/order/getBotConfig?' + data,{headers: this.headers});
  }

  public createOrder(data) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json');
    let params = data;
    return this.http.post(this.url8080 + '/order/create', params, {headers: head}).pipe(map(val => {
      return val;
    })
    );
  }

  public scheduleOrder(data) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json');
    let params = data;
    return this.http.post(this.url8080 + '/order/createSchduler', params, {headers: head}).pipe(map(val => {
      return val;
    })
    );
  }

  public scheduleOrderUpdate(data) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json');
    let params = data;
    return this.http.post(this.url8080 + '/order/updateSchedulerById', params, {headers: head}).pipe(map(val => {
      return val;
    })
    );
  }

  public getOrderType() {
    return this.http.get(this.url1 + '/order/getOrderType',{headers: this.headers});
  }

  // public getScheduleList(){
  //   return this.http.get(this.url8080 + '/order/getScheduleList');
  // }
  public roleTypeuser(user_id){
    
    
    return this.http.get(this.url1+'/order/getUserRoles?userId=' + user_id,{headers: this.headers});
  }
  public getScheduleList(roles){
    let data = 'role=';
    roles.forEach((element, index) => {
      if (index == 0) {
        data = data + element.roleId;
      } else {
        data = data + ',' + element.roleId;
      }

    });
    return this.http.get(this.url8080 + '/order/getScheduleList?' + data,{headers: this.headers});
  }

  public getSchedularDetails(schedular_id){
    return this.http.get(this.url8080 + '/order/getSchedulerDetailsById?schedulerId=' + schedular_id,{headers: this.headers});
  }

  public getSuitAndBotDetails(schedular_id){
    return this.http.get(this.url8080 + '/order/getSuiteDetailsBySchedulerId?schedulerId=' + schedular_id,{headers: this.headers});
  }

  public deleteSchedule(id){
    return this.http.delete(this.url8080 + '/order/deleteScheduler?schedulerId=' + id,{headers: this.headers});
  }

  public pauseSchedule(id) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let head= this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json');
    let params = {};
    return this.http.post(this.url8080 + '/order/pauseScheduledOrder?schedulerId=' + id, params, {headers: head}).pipe(map(val => {
      return val;
    })
    );
  }

  public resumeSchedule(id) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Cache-Control': 'no-cache' })
    };
    let head = this.headers;
    head = head.append('Cache-Control', 'no-cache');
    head = head.append('Content-Type', 'application/json');
    let params = {};
    return this.http.post(this.url8080 + '/order/resumeScheduledOrder?schedulerId=' + id, params, {headers: head}).pipe(map(val => {
      return val;
    })
    );
  }
}
