import { Component, OnInit, Input, OnChanges, SimpleChange } from '@angular/core';

import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-scheduled-job-view',
  templateUrl: './scheduled-job-view.component.html',
  styleUrls: ['./scheduled-job-view.component.scss']
})
export class ScheduledJobViewComponent implements OnInit {
  @Input() suitDetails: any;
  public selectedBot :any
  inputtext:any
 
  pipe = new DatePipe('en-US');

cronExecationarray=[]
  constructor( ) {
     
   }
   
  ngOnInit(): void {
     
     
 


  }


  ngOnChanges(changes:SimpleChange)
   {
      // console.log(this.suitDetails);
//       var currentValue=changes['suitDetails']['currentValue']
//  console.log("ch",changes['suitDetails']['currentValue'])
//       console.log("thisbot",this.selectedBot)
this.cronExecationarray=[]
      //    console.log('datearray',this.suitDetails.cronNextExecutionDates);
         this.selectedBot=this.suitDetails.botInfo.suiteDetails[0].botDetails[0]
if(this.suitDetails.hasOwnProperty('cronNextExecutionDates'))
  {
   this.suitDetails.cronNextExecutionDates.forEach(element => {
      var changedate=this.pipe.transform(element,"dd/MM/yyyy HH:mm")
      //var changedate=moment(element).format("dd/MM/yyyy HH:mm")
      this.cronExecationarray.push(changedate)
    });
  }
    //  console.log("datenewarray",this.cronExecationarray)    
 
   }
 
  
  getBotDetailsView(bot) {
    this.selectedBot=''
  
    
    
    this.selectedBot = bot;
  }
  
}
