    if (i.length > 0) {
      this.readWriteFlag = true;
    }
    
    
    setTimeout(function () {
      this.intakes.forEach(element => {
        if(element.team.ldapRole!= null){
          let k = this.roles.filter(item => item.roleId === element.team.ldapRole.roleId);
            if (k.length > 0) {
            element.automateFlag = true;
          }
        }
      });
    }.bind(this), 5000); 
