  deleteIntake(intake, index) {
    this.alertIndex = index;
    this.intakeService.deleteIntake(intake.intakeId).subscribe(
      val => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "success", "close": true,
          "content": "Intake deleted successfully",
        }];
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
      },
      error => {
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "<strong>Failed</strong> to delete intake",
        }
        ];
        setTimeout(function () {
          this.alertdata = false;
          window.location.reload();
        }.bind(this), 1000);
      });
  }

  viewIntake(intake, index) {
    this.viewData = intake;
    this.viewindex = index;
  }

  approveIntake(intake, index) {
    this.alertIndex = index;
    let comment = (<HTMLSelectElement>document.getElementById("approverCommentsAll_" + index)).value;
    if (comment != '' && comment != undefined && comment.trim().length > 0){
      this.viewfield=true
      let approve = {
        intakeId: intake.intakeId,
        approver: {
          id: this.user.id
        },
        approvercomments: (<HTMLSelectElement>document.getElementById("approverCommentsAll_" + index)).value,
        rfStatus: {
          status: 'APPROVED'
          // statusdesc: 'approved'
        }
      };
  
      this.intakeService.approveIntake(approve).subscribe(
        val => {
          this.approvalMessage = val;
          let maildata = {
            mailContent: '<b>' + intake.ideaName + '</b> has been approved by <b>' + this.user.name + '</b> on ' + this.datePipe.transform(new Date().toISOString(), 'yyyy-MM-dd HH:mm:ss'),
            to: '',
            cc: '',
            Bcc: '',
            testingRecipients: {
              to: intake.createdby.email,
              cc: this.commonApprover.email.value + ',' + this.user.email,
              Bcc: 'arpan.jyoti@prodapt.com,ravichandran.k@prodapt.com,gayathri.sk@prodapt.com,praveen.ch@prodapt.com,anusudha.s@prodapt.com,williamedilbert.pr@prodapt.com'
            },
            subject: 'INTAKE-Approved'
          };
          if(this.approvalMessage.message  && this.approvalMessage.message.includes('failed') && this.approvalMessage.message.includes('Jira')) {
            maildata.mailContent += '<br><br><span style="color:red;"><b>Note:</b> Failed to update in Jira.</span>';
          }
          this.loginService.sendMail(JSON.stringify(maildata)).subscribe(
            val1 => {
              this.message = val1;
              if(this.approvalMessage.message && this.message.response == 'Mail Sent Successfully') {
                this.alertdata = true;
                this.alertMessage = [{
                  "type": "success", "close": true,
                  "content": this.approvalMessage.message,
                }];
                setTimeout(function () {
                  this.alertdata = false;
                  window.location.reload();
                }.bind(this), 1000);
              } else if (this.message.response == 'Mail Sent Successfully') {
                this.alertdata = true;
                this.alertMessage = [{
                  "type": "success", "close": true,
                  "content": "Intake approved successfully",
                }];
                setTimeout(function () {
                  this.alertdata = false;
                  window.location.reload();
                }.bind(this), 1000);
              } else {
                this.alertdata = true;
                this.alertMessage = [{
                  "type": "danger", "close": true,
                  "content": "<strong>Failed</strong> to send mail",
                }
                ];
                setTimeout(function () {
                  this.alertdata = false;
                  window.location.reload();
                }.bind(this), 1000);
              }
            });
        });
    }else{
      this.alertdata = true;
      this.alertMessage = [{
        "type": "danger", "close": true,
        "content": "Kindly provide comments before Approval!",
      }
      ];
      setTimeout(function () {
        this.alertdata = false;
        //window.location.reload();
      }.bind(this), 1000);
    }
   
  }

