  EditBenefitsFormNew(val):FormGroup{
    return this.formBuilder.group({
      benefitId:[val.businessBenefitId],
      ideaBenefitName:[val.businessBenefit],
      description:['',[Validators.required, Validators.maxLength(500)]],
      mapId:[0],
    });
  }

  addBusinessBenefitNew(val):void {
    this.benefitsFormEdit().push(this.EditBenefitsFormNew(val));
  }

  /*/////////////////Edit Form Exiting DATA//////////////////////*/
  EditBenefitsForm(benefit):FormGroup{
    return this.formBuilder.group({
      benefitId:[benefit.businessBenefit.businessBenefitId],
      ideaBenefitName:[benefit.businessBenefit.businessBenefit],
      description:[benefit.description,[Validators.required, Validators.maxLength(500)]],
      mapId:[benefit.mapId],
    });
  }

  addBusinessBenefit(benefit):void {
    this.benefitsFormEdit().push(this.EditBenefitsForm(benefit));
  }

