  onItemDeSelect(event: any){
    this.indexValueSbi = -1;
    for(var i=0;i<this.intakeData.rfEffort.device.length;i++){
      if(this.intakeData.rfEffort.device[i].devicename == event.sbitype){
        this.indexValueSbi = i;
        break;
      }
      else if(this.intakeData.rfEffort.device[i].otherFlag == event.sbitype){
        this.indexValueSbi = i;
        break;
      }
    }
    if (this.indexValueSbi > -1) {
      this.intakeData.rfEffort.device.splice(this.indexValueSbi, 1);
    }
    if(event.sbitype == 'Other (Specify)'){
      this.otherDeviceName = false
    }
    let count = 0;
    setTimeout(function(){
      for(var i=0; i<this.intakeData.rfEffort.device.length; i++){
        if((<HTMLSelectElement>document.getElementById('deviceRole_edit_'+i)).value == ''){
          count++;
          break;
        }
      }
    }.bind(this),1000);
    if(count !=0  || this.intakeData.rfEffort.device.length!=this.SBItableLength.length){
      this.sbiRoleInvalid = true;
    }else{
      this.sbiRoleInvalid = false;
    }
  }
  otherFieldValidation(event, i){
    if(event == ''){
      this.otherDeviceName = true;
    }
    else{
      this.otherDeviceName = false;
      this.intakeData.rfEffort.device[i].devicename = event;
      this.deviceapplicationNames=event
    }

  }
  onNbiItemSelect(item: any){
    let pojo ={};
    pojo['roleInAutomation'] = '';
    this.roleInvalidNbi = true;
    if(item.nbitype == 'Other (Specify)'){
      pojo['applicationName'] = "";
      pojo['otherFlag'] = "Other (Specify)";
      this.intakeData.rfEffort.application.push(pojo);
      this.otherApplicationName = true;
    }else{
      pojo['applicationName'] = item.nbitype;
      pojo['otherFlag'] = "";
      this.intakeData.rfEffort.application.push(pojo);
    }

  }
  onItemDeSelectApp(event:any){
    this.indexValueNbi = -1;
    for(var i=0; i<this.intakeData.rfEffort.application.length; i++){
      if(this.intakeData.rfEffort.application[i].applicationName == event.nbitype){
        this.indexValueNbi = i;
        break;
      }
        else if(this.intakeData.rfEffort.application[i].otherFlag == event.nbitype){
          this.indexValueNbi = i;
          break;
        }
    }

    if (this.indexValueNbi > -1) {
      this.intakeData.rfEffort.application.splice(this.indexValueNbi, 1);
    }
    if(event.nbitype == 'Other (Specify)'){
      this.otherApplicationName = false;
      this.otherDeviceSbi.sbitype = "";
    }
    let count = 0;
    setTimeout(function(){
      for(var i=0; i<this.intakeData.rfEffort.application.length; i++){
        if((<HTMLSelectElement>document.getElementById('applicationRole_edit_'+i)).value == ''){
          count++;
          break;
        }
      }
    }.bind(this),1000);

    if(count !=0 || this.intakeData.rfEffort.application.length != this.NBItableLength.length){
      this.roleInvalidNbi = true;
    }
    else{
      this.roleInvalidNbi= false;
    }

  }
