  calculateRatio() {
    this.ratio = (this.intakeEdit.value.dataInterpretation * 1) + (this.intakeEdit.value.manualActivity * 1) + (this.intakeEdit.value.decisionMaking * 1);
    this.ratio = this.intakeEdit.value.aet - this.ratio;
    this.ratio = (this.ratio * 100) / this.intakeEdit.value.aet;
    this.ratio = this.ratio.toFixed();
    this.intakeEdit.patchValue({
      automationRatio: this.ratio,
      dataInterpretation: this.intakeEdit.value.dataInterpretation * 1,
      manualActivity: this.intakeEdit.value.manualActivity * 1,
      decisionMaking: this.intakeEdit.value.decisionMaking * 1,
    });
  }

  selectBusinessUnit(event: any) {
    // console.log(this.approver);
    if (event.target.value === "") {
      this.approver = {};
      this.intakeEdit.patchValue({
        // primaryApprover: '',
        benefittingDirector: ''
      });
    } else {
      this.approver = this.businessUnitList.filter(item => item.id === (event.target.value * 1));
      this.approver = this.approver[0];
      this.selectedBusinessUnit = this.approver.teamName;
      this.approver = this.approver.department.director;
      this.intakeEdit.patchValue({
        // primaryApprover: this.commonApprover.role.roleId,
        benefittingDirector: this.approver.name
      });
    }
  }

