  onFileSelect(input: HTMLInputElement): void {
    console.log("arraylist",this.filenamearray,this.finalfilearray)
    this.totalsize=this.checkFilesize(input)
    for (let i = 0; i < input.files.length; i++) {
      if (this.allowedType.includes(input.files[i].type)==true && this.totalsize <= 5242880 && this.filenameduplication(input.files[i].name)==false) {
        this.fileInfo = this.fileInfo.concat(input.files[i]);
        this.filenamearray.push(input.files[i].name)
      } else {
       // console.log('Please select Word Documents less than 5MB');
        this.alertdata = true;
        this.alertMessage = [{
          "type": "danger", "close": true,
          "content": "Please select the documents of size less than 5MB(Accepted file formats: .pdf, .docx, .doc, .pptx, .csv, .xls, .xlsx)",
        }];
        if(this.filenameduplication(input.files[i].name)==true && this.filenamearray.length > 0)
        {
           
          this.alertMessage=[{
            "type": "danger", "close": true,
            "content":input.files[i].name +' is already  been added',
          }]
        }
        setTimeout(function () {
          this.alertdata = false;
          // window.location.reload();
        }.bind(this), 3000);
      }
    }
    this.fileInputs.nativeElement.value = '';
    console.log('files', this.fileInfo);
  }

